
import { Component } from '@angular/core';
import { App, Platform, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { Observable } from 'rxjs/Observable';

import { LoopBackConfig } from '../../shared/sdk';
declare const lbclient: any;

import { BaseServiceProvider } from '../providers/base-service/base-service';
import { SiteconfigServiceProvider } from '../providers/siteconfig-service/siteconfig-service';
import { ConfigServiceProvider } from '../providers/config-service/config-service';
import { SharedServiceProvider } from '../providers/shared-service/shared-service';
import { SodServiceProvider } from '../providers/sod-service/sod-service';
import { CustomerconnServiceProvider } from '../providers/customerconn-service/customerconn-service'
import { PdaparamsServiceProvider } from '../providers/pdaparams-service/pdaparams-service';
import { ConnectivityMonitorProvider } from '../providers/connectivity-monitor/connectivity-monitor';
import { MessageServiceProvider } from '../providers/message-service/message-service';
import { JseaServiceProvider } from '../providers/jsea-service/jsea-service';
import { ImageServiceProvider } from '../providers/image-service/image-service';
import { DeviceDiagnosticServiceProvider } from '../providers/device-diagnostic-service/device-diagnostic-service';
import { SqliteServiceProvider } from '../providers/sqlite-service/sqlite-service';
import { LogServiceProvider } from '../providers/log-service/log-service';




@Component({
  templateUrl: 'app.html'
})

export class MyApp {

  allowed: boolean = false;
  connDetails;
  userId;
  result;
  Job = lbclient.models.LocalJob;
  rootPage: any;
  startTime;

  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'app.component.ts' };
  log = this.logger.getInstance(this.logParams);

  constructor(public app: App, public storage: SqliteServiceProvider, public statusBar: StatusBar,
    private alertCtrl: AlertController, public splashScreen: SplashScreen,
    private pdaParams: PdaparamsServiceProvider, private platform: Platform,
    private connectivityMonitor: ConnectivityMonitorProvider,
    private base: BaseServiceProvider, private sharedService: SharedServiceProvider,
    private customerConn: CustomerconnServiceProvider, private siteConfig: SiteconfigServiceProvider,
    private configService: ConfigServiceProvider, private sodService: SodServiceProvider,
    private messageService: MessageServiceProvider, private jseaService: JseaServiceProvider,
    private imageService: ImageServiceProvider,
    private deviceDiagnosticService: DeviceDiagnosticServiceProvider,
    private logger: LogServiceProvider) {

    console.log("Hello app.component.ts");

    (async () => {

      await this.platform.ready().then((readySource) => {
        console.log('Platform ready from ::', readySource);
        // Platform now ready, execute any required native code       
		//
		// NOTE - logging in this component may well be logged as driver 0 so be aware
		this.log.info("constructor");
      });


      //0. Make sure the sqlite storage is opened and ready for work
      if (!this.sharedService.desktopTesting) {

        if (!this.sharedService.isDbOpen) {
          await this.storage.opendbOnDeviceReady().then((res: boolean) => {
            this.sharedService.isDbOpen = res;
            console.log("this.sharedService.isDbOpen in the app.component.ts::" + this.sharedService.isDbOpen);
            this.log.info("this.sharedService.isDbOpen in the app.component.ts::" + this.sharedService.isDbOpen);
          });
        }
      }


      //1.Is the user logged in?
      //2. No,the user is not logged in. 
      if (!(localStorage.getItem('login'))) {
        console.log('There is no login available in local storage. SG CHANGE');
        this.log.info('There is no login available in local storage. SG CHANGE');
        var loginVar;
        //2.1. Check if it is available in the sqlite db    
        if (!this.sharedService.desktopTesting) {

          /****** SG CHANGE TO HAVE a TEST for IOS *****/
          await this.storage.get("login").then((val) => {
            if (val) {
              loginVar = val;
              console.log('There is a login available in sqlite storage.');
              this.log.info('There is a login available in sqlite storage.');
            }
            else {
              loginVar = null;
              console.log('There is NO login available in sqlite storage.');
              this.log.info('There is NO login available in sqlite storage.');
            }

            this.sharedService.localStorageZapped = loginVar;
          });
        }

        //2.2. Is the localstorage zapped?
        if (this.sharedService.localStorageZapped) {
          console.log('LocalStorage has been zapped!!');
          /*
          * 2.3 If yes,localstorage has been zapped, retrive all variables from sqlite and write to localstorage 
          * */
          localStorage.setItem('login', loginVar);

          console.log('LocalStorage has been zapped, so we copy the contents of SqliteStorage to LocalStorage.');
          this.log.info('LocalStorage has been zapped, so we copy the contents of SqliteStorage to LocalStorage.');
          await this.storage.getAndSetToLocalStorage().then((val: any) => {
            //Also, set firstlogin to false and redirecting to index.html to sync with lbclient
            localStorage.setItem('firstlogin', 'false');
            document.location.href = 'index.html';
            return;

          });
        }

        //2.4. localstorage is not zapped.
        //
        else {
          console.log('LocalStorage has NOT been zapped, so we are redirected to the login page.');
          this.log.info('LocalStorage has NOT been zapped, so we are redirected to the login page.');
          //clear previous data in the storage, if any.
          localStorage.clear();
          this.storage.clear();
          //Set the firstime login variable
          localStorage.setItem('firstlogin', 'true');
          //this.splashScreen.hide();
          //Direct him to the login page..
          this.rootPage = "LoginPage";
          return;
        }

      }
      /*    }
         else {
           localStorage.setItem('firstlogin', 'true');
           this.rootPage = "LoginPage";
           return;
         }
    */
      //set the tab string in shared-service
      this.sharedService.tab = "App.Component.ts";

      //3. Is the Server API Url set?
      //4. No, the Server API Url is not set. Get & Set the the Server API Url.
      //Also set the server path for the loopback SDK builder in the ./shared/sdk directory.
      if (!(localStorage.getItem('apiURL'))) {
        this.userId = localStorage.getItem('userId');

        //Hack:Set userid into sqlite as its not being set from login.ts
        if (!this.sharedService.desktopTesting) {
          this.storage.set("userId", this.userId).then((res) => {
            if (res) {
              console.log("userid is set");
              this.log.debug("userid is set");
			}
            else {
              console.log("userid is NOT set");
              this.log.debug("userid is NOT set");
			}
          });
       }


        //console.log('userId====' + this.userId);
        this.connDetails = this.customerConn.getConnectionDetails(this.userId.toLowerCase());
        this.configService.setSiteDetails(this.connDetails);

        if (!localStorage.getItem('clientId'))
          localStorage.setItem('clientId', this.connDetails.clientId);

        //console.log("================The site id is===================::" + this.pdaParams.getSiteId());
        //console.log("lbclient.dataSources.adduserid"+lbclient.dataSources.adduserid);

        //Set the server path for the loopback SDK builder in the ./shared/sdk directory.     
        LoopBackConfig.setBaseURL((this.connDetails.url));
        LoopBackConfig.setApiVersion('api');
      }



      //5.Check & set All Config data
      if (!(localStorage.getItem('SITE_CONFIG_LOADED'))) {
        //console.log("Getting the siteconfigs!");
        this.userId = localStorage.getItem('userId');
        this.connDetails = this.customerConn.getConnectionDetails(this.userId.toLowerCase());
        LoopBackConfig.setBaseURL((this.connDetails.url));
        LoopBackConfig.setApiVersion('api');

        let result$: Observable<string> = this.siteConfig
          .loadAllConfigsFromServer()
          .subscribe((result) => {
            console.log("Site Config Loaded!");

            this.commonCode();
            //this.sharedService.SITE_CONFIG_LOADED.next(true);
          },
            (error) => {
              this.showError("Unable to load Site Configs.Please check your connection.");
              return;
            });
      }
      else {

        this.commonCode();
        //this.sharedService.SITE_CONFIG_LOADED.next(true);
      }
    })();
  }//constructor ends here

  //Shared code which should executes after siteconfigs are loaded.
  commonCode = () => {
    this.platform.ready().then(() => {
      //Riyaz:Take care of the android hardware back button press.
      this.platform.registerBackButtonAction(() => {
        // Catches the active view
        let nav = this.app.getActiveNavs()[0];
        let activePage = nav.getActive().name;
        // Checks if can go back before show up the alert
        if (nav.canGoBack()) {
          nav.pop();
        } else {
          const alert = this.alertCtrl.create({
            title: 'Warning',
            message: 'This will close the app and disable GPS tracking.  Are you sure you want to exit?',
            buttons: [{
              text: 'Cancel',
              role: 'cancel',
              handler: () => {
                nav.setRoot(activePage);
              }
            }, {
              text: 'OK',
              handler: () => {
                let mystr = 'backbutton: operator selected OK to exit app, tab:' + activePage;
                //this.log.info(mystr);
                this.platform.exitApp();
              }
            }],
            enableBackdropDismiss: false
          });
          alert.present();
        }

      });
    });


    //6. Check & set all table metadata
    //6.1 Load the jobs metadata  

    let result: Observable<any> = this.base.getModelMetadata(this.Job, "jobMetadata");
    this.sharedService.jobMetaDataLoadedSubject.subscribe();

    //7. Check & set all watchers
    //7.1 Start monitoring network connectivity    
    this.connectivityMonitor.startWatching();

    //7.2 Start monitoring for JSEA changes.
    this.jseaService.startWatching();

    //7.3 check if this is first start for the day
    this.sodService.checkDoSodAction('FIRST_RESUME');

    //7.4  Start monitoring images for auto upload 
    this.imageService.startWatching();

    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.        
      //this.statusBar.styleDefault();
      this.statusBar.overlaysWebView(false);
      //this.statusBar.styleLightContent();
      // set status bar to black
      this.statusBar.backgroundColorByHexString('#000000');

      // Start monitoring for messages  
      this.messageService.startWatching();

      //Start monitoring the device diagnostics
      this.deviceDiagnosticService.startWatching();
      
      //hide splashscreen
      //this.splashScreen.hide();

    });

    //8. If driverid > 0, redirect to jobs page
    if (this.pdaParams.getDriverId() > 0) {
      this.rootPage = "TabsPage";
    }
    else {
      //9. If driverid <=0, redirect him to PDA page.     
      this.rootPage = "PdaPage";
    }
  }//commonCode() ends here

  showError(text) {
    let alert = this.alertCtrl.create({
      title: 'Error',
      subTitle: text,
      buttons: ['OK'],
      enableBackdropDismiss: false
    });
    alert.present();
  }

}



