import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { LogServiceProvider } from '../log-service/log-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { ConfigServiceProvider } from '../config-service/config-service';
import { WindowRefServiceProvider } from '../window-ref-service/window-ref-service';

/*
  Generated class for the EventServiceProvider provider.

  Send a message to the mobile_event REST API.Used for e.g. LOGON, LOGOFF, maybe sending a message etc.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class EventServiceProvider {

  constructor(public http: Http, private logger: LogServiceProvider,
    private pdaParams: PdaparamsServiceProvider,
    private configService: ConfigServiceProvider,
    private windowRef: WindowRefServiceProvider) {
    //console.log('Hello EventServiceProvider Provider');
  }

  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'EventServiceProvider' };
  log = this.logger.getInstance(this.logParams);

  sendMsg = (msgType, msgData?) => {
    var message = <any>{};
    var data = msgData || '';

    if (typeof (data) === 'object') {
      message.data = data;		// put object in as is
    }
    else {
      message.data = {
        driverId: this.pdaParams.getDriverId(),
        appVersion: this.pdaParams.getAppVersion(),
        messageText: data,
        deviceHost: this.windowRef.nativeWindow.location.host			// NOTE - this works in a browser only
      };
    }

    message.event = msgType;
    // var serverIP = this.configService.getServerIP();
    //var serverPort = this.configService.getServerPort();
    var apiURL = this.configService.getBaseUrl();

    this.log.info(JSON.stringify(message));

    var compURL = apiURL + '/api/mobile_event' + '?payload=' + encodeURIComponent(JSON.stringify(message));

    this.http.get(compURL).map(res => res.json()).subscribe(data => {
      //console.log("Data = " + data);
    });
  }

}



