import { Injectable } from '@angular/core';
/*
  Generated class for the UtilServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class UtilServiceProvider {
  startTime;
  replTime;
  oneDay = 60 * 60 * 24 * 1000;
  scrollBarWidth = null;

  constructor() {
    //console.log('Hello UtilServiceProvider Provider');
  }


  dateDiff(older, newer) {
    var data = <any>{};
    // Elapsed milliseconds
    data.elapsed = newer.getTime() - older.getTime();
    // Elapsed Days
    data.days = Math.round((((data.elapsed / 1000) / 60) / 60) / 24);
    // Elapsed Hours
    data.hours = Math.round(((data.elapsed / 1000) / 60) / 60);
    // Elapsed Months
    var monthDiff = (newer.getMonth() + 1) - (older.getMonth() + 1);
    var yearDiff = newer.getFullYear() - older.getFullYear();
    data.months = monthDiff + (yearDiff * 12);
    // Return data
    return data;
  }

  arraysIdentical(array1, array2) {
    return (JSON.stringify(array1) === JSON.stringify(array2));
  }

  trimLeadingSpaces(s) {
    var index = 0;
    var lastLeadingString = -1;
    while (s.charCodeAt(index) < 33) {
      lastLeadingString++;
      index++;
    }
    if (lastLeadingString >= 0) {
      var sTrim = s.substr(lastLeadingString + 1);
      return sTrim;
    }
    else {
      return s;
    }
  }

  nameSort(a, b) {
    if (a.name < b.name) {
      return -1;
    }
    if (a.name > b.name) {
      return 1;
    }
    return 0;
  }


  findArrayIndexByKey(data, key, value) {
    for (var i = 0; i < data.length; i++) {
      if (data[i][key] === value) {
        return i;
      }
    }
    return -1;
  }

  findArrayItemByKey(data, key, value) {
    //console.log("data in findArrayItemByKey::::" + data);
    for (var i = 0; i < data.length; i++) {
      if (data[i][key] === value) {
        return data[i];
      }
    }
    return null;
  }

  findArrayItemsByKey(data, key, value) {
    var retArray = [];
    for (var i = 0; i < data.length; i++) {
      if (data[i][key] === value) {
        retArray.push(data[i]);
      }
    }
    return retArray;
  }


  isNumber(n) {
    return !isNaN(parseFloat(n)) && isFinite(n);
  }

  getParamFromURL(url, key) {
    var params = url.split("?");
    var value = "";
    if (params.length > 1) {
      params = params[1].split("&");
      for (var i = 0; (i < params.length) && (value === ""); i++) {
        if (params[i].indexOf(key) === 0) {
          var param = params[i].split("=");
          if (param.length > 1) {
            value = param[1];
          }
        }
      }
    }
    return decodeURIComponent(value);
  }



  getScrollbarWidth() {
    if (this.scrollBarWidth !== null) {
      return this.scrollBarWidth;
    }
    var outer = document.createElement("div");
    outer.style.visibility = "hidden";
    outer.style.width = "100px";
    outer.style.msOverflowStyle = "scrollbar"; // needed for WinJS apps

    document.body.appendChild(outer);

    var widthNoScroll = outer.offsetWidth;
    // force scrollbars
    outer.style.overflow = "scroll";

    // add innerdiv
    var inner = document.createElement("div");
    inner.style.width = "100%";
    outer.appendChild(inner);

    var widthWithScroll = inner.offsetWidth;

    // remove divs
    outer.parentNode.removeChild(outer);

    this.scrollBarWidth = widthNoScroll - widthWithScroll;
    return this.scrollBarWidth;
  }

  /*  ShowDataErrorMessage(value, message) {
     if ((value === "Error") || (value === "")) {
       if (message === "") {
         message = "An Error has occured retrieving your data.  Please try again or return to the Previous Page.";
       }
       $(".tabContentContainer").html("");
       $("<div class='dataError'>" + message + "</div>").appendTo($(".tabContentContainer"));
       $(".tabContentContainer")[0].scrollIntoViewIfNeeded(false);
       return true;
     }
     else {
       return false;
     }
 
   } */



  /*  GetResponsiveScreenSize() {
     var visible = false;
     visible = $("#Screen-XS").is(":visible");
     if (visible === true) {
       return "XS";
     }
     visible = $("#Screen-SM").is(":visible");
     if (visible === true) {
       return "SM";
     }
     visible = $("#Screen-MD").is(":visible");
     if (visible === true) {
       return "MD";
     }
     visible = $("#Screen-LG").is(":visible");
     if (visible === true) {
       return "LG";
     }
     return "";
   } */

  copyObject(obj) {
    return JSON.parse(JSON.stringify(obj));
  }

  arrayUnique(array, property) {
    var a = array.concat();
    for (var i = 0; i < a.length; ++i) {
      for (var j = i + 1; j < a.length; ++j) {
        if (property === undefined) {
          if (a[i] === a[j]) {
            a.splice(j--, 1);
          }
        }
        else {
          var x = a[i][property];
          var x2 = a[j][property];
          if (x === x2) {
            a.splice(j--, 1);
          }
        }
      }
    }

    return a;
  }


  isEmpty = (obj) => {
    for (var key in obj) {
      if (obj.hasOwnProperty(key))
        return false;
    }
    return true;
  }
  executionTime = (str) => {
    const duration = performance.now() - this.startTime;
    //console.log(`============executionTime() upto ${str} took ${duration}ms======================`);
  }

  supplant(str, o) {
    return str.replace(
      /\{([^{}]*)\}/g,
      function (a, b) {
        var r = o[b];
        return typeof r === 'string' || typeof r === 'number' ? r : a;
      }
    );
  };

  getFormattedTimestamp(date) {
    return this.supplant('{0}:{1}:{2}:{3}', [
      date.getHours(),
      date.getMinutes(),
      date.getSeconds(),
      date.getMilliseconds()
    ]);
  };
  getLogTimestamp(date) {
    return (date.toISOString());
  };

  getLocalTimestamp(date) {
    var tzo = -date.getTimezoneOffset();
    var dif = tzo >= 0 ? '+' : '-';
    function pad(num) {
      var norm = Math.abs(Math.floor(num));
      return (norm < 10 ? '0' : '') + norm;
    };
    return date.getFullYear()
      + '-' + pad(date.getMonth() + 1)
      + '-' + pad(date.getDate())
      + 'T' + pad(date.getHours())
      + ':' + pad(date.getMinutes())
      + ':' + pad(date.getSeconds())
      + dif + pad(tzo / 60)
      + ':' + pad(tzo % 60);
  };




}



