
//import { Injectable, ReflectiveInjector } from '@angular/core';
import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
//import 'reflect-metadata';
import { ConfigServiceProvider } from '../config-service/config-service';
import { SharedServiceProvider } from '../shared-service/shared-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { UtilServiceProvider } from '../util-service/util-service';
import { LoggerService } from '../../../shared/sdk/services/custom/logger.service';
import { Platform } from 'ionic-angular';

import { LogServiceInstance } from './log-service-instance';

/*
  Generated class for the LogServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class LogServiceProvider extends LoggerService {

  /*  private pdaParamService;
   private configService;
   private loggerService;
   //public http; */


  constructor(
    private pdaParamService: PdaparamsServiceProvider,
    private configService: ConfigServiceProvider, public http: Http,
    private sharedService: SharedServiceProvider, public platform: Platform,
    public utilService: UtilServiceProvider) {
    super();
  }

  context;
  pdaParams;
  _log;

  /*  enabled(_isEnabled) {
     this.isEnabled = !!_isEnabled;
   }; */

  logInstance(context) {
    //console.log("=========The Driver ID in the logService.ts is ::============" + context.driver);
    context.ver = this.configService.appConfig.version;
    this.context = context;

    this.pdaParams = this.pdaParamService;
    //context, driverId, logServerIP, logServerPort
    return new LogServiceInstance(this.http, this.context, this.pdaParamService.getDriverId(), this.getlogServerIP(), this.getlogServerPort(), this.pdaParams, this.sharedService, this.platform, this.utilService);
  };


  getInstance(context) {
    //return new Logger(context);
    return this.logInstance(context);
  };

  setContext(context) {
    this.context = context;
  }

  getlogServerIP() {
    return (this.configService.appConfig.logServerIP);
  };

  getlogServerPort() {
    return (this.configService.appConfig.logServerPort);
  };
  /*
    debug(arg: string) {
      if (LoopBackConfig.debuggable())
        console.debug(arg);
    }
    warn(arg: string) {
      if (LoopBackConfig.debuggable())
        console.warn(arg);
    } */

}


