import { Injectable } from '@angular/core';


declare const lbclient: any;
interface optionsInterface {
  filter: string;
  lastFilter: string;
};
interface sinceInterface {
  push: number;
  pull: number;
}

/*
  Generated class for the JseaAnswersReplicationServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class JseaAnswersReplicationServiceProvider {

  LocalJseaDriverAnswers = lbclient.models.LocalJseaDriverAnswers;
  RemoteJseaDriverAnswers = lbclient.models.RemoteJseaDriverAnswers;
  models = lbclient.models;
  lastFilter = {};
  d = new Date();
  isOnline;
  since: sinceInterface = { push: -1, pull: -1 };

  constructor() {
    //console.log('Hello JseaAnswersReplicationServiceProvider Provider');

    this.LocalJseaDriverAnswers.on('before save', function (ctx, next) {
      next();
    });

    // sync local changes if connected
    this.LocalJseaDriverAnswers.on('after save', function (ctx, next) {
      next();
      this.jseaAnswers_sync();
    });
    this.LocalJseaDriverAnswers.on('after delete', function (ctx, next) {
      next();
      this.jseaAnswers_sync();
    });

  }

  jseaAnswers_sync = (callback, filter) => {

    var options: optionsInterface = { filter: '', lastFilter: '' };
    var cb = null;
    cb = callback;
    options.filter = filter;
    options.lastFilter = filter;
    this.lastFilter = filter;

    var counter = 0;

    //console.log(new Date().toISOString() + ': starting replication');

    // LT - 10/12/2015 - original code - does remote first
    this.LocalJseaDriverAnswers.replicate(
      this.since.push,
      this.RemoteJseaDriverAnswers,
      options,
      (err, conflicts, cps) => {
        this.since.push = cps;
				/* 	LocalJseaDriverAnswers.replicate(
						since.push,
						RemoteJseaDriverAnswers,
						options,
						function pulled(err, conflicts, cps) {
							since.pull = cps; */
        //console.log(new Date().toISOString() + ': finished replication');
        cb && cb(err, conflicts);
        /* }); */
      });

  }


}



