import { Injectable } from '@angular/core';



import { AlertController } from 'ionic-angular';

import { Device } from '@ionic-native/device';
import { Push, PushObject } from '@ionic-native/push';
import { MediaObject } from '@ionic-native/media';


import { LogServiceProvider } from '../log-service/log-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { PlatformReadyServiceProvider } from '../platform-ready-service/platform-ready-service';
import { SharedServiceProvider } from '../shared-service/shared-service';
import { UtilServiceProvider } from '../util-service/util-service';
import { SiteconfigServiceProvider } from '../siteconfig-service/siteconfig-service';

import { InstallationApi } from '../../../shared/sdk/services';///shared/sdk/services';
import { ExitServiceProvider } from '../exit-service/exit-service';
import { SqliteServiceProvider } from '../../providers/sqlite-service/sqlite-service';

/*
  Generated class for the PushServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/

@Injectable()
export class PushServiceProvider {
  pushObject: PushObject;

  /* "senderID" is "Project Number" from Google Developers Console (https://console.firebase.google.com)
    NOTE that there is also a server component involved - see API key in /app/strongloop/OSC-API/server/gcm_config.js */

  androidConfig = {
    senderID: ""
  };

  iosConfig = {
    badge: true,
    sound: true,
    alert: true
  };
  browserConfig = {
    pushServiceURL: 'http://push.api.phonegap.com/v1/push'
  };

  winConfig = {};

  pushOptions: any = {
    android: this.androidConfig,
    ios: this.iosConfig,
    windows: this.winConfig,
    browser: this.browserConfig
  };

  registered = false;
  already_registered = false;

  notificationSnd: MediaObject;
  nagPopup;
  nagInterval;
  already_nagging = false;
  lastSoundTime = Math.round(new Date().getTime() / 1000);		// init when service instantiates
  newSoundTime;

  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'PushServiceProvider' };
  log = this.logger.getInstance(this.logParams);


  constructor(private pdaParams: PdaparamsServiceProvider, public storage: SqliteServiceProvider,
    private logger: LogServiceProvider, private platformReady: PlatformReadyServiceProvider,
    private device: Device, private push: Push, private installationApi: InstallationApi,
    public alertCtrl: AlertController, private sharedService: SharedServiceProvider,
    private utilService: UtilServiceProvider, private siteConfig: SiteconfigServiceProvider) {
    //console.log('Hello PushServiceProvider Provider');
  }


  registerForPush() {
    //console.log("Register for push called!");
    var pda_sender_id = this.siteConfig.getSiteConfigValue('PDA_SENDER_ID');

    if (pda_sender_id) {
      this.pushOptions.android.senderID = pda_sender_id;
    }

    this.log.debug('registerForPush: isready:' + this.platformReady.isPlatformReady() + ', driverId:' + this.pdaParams.driverId + ', registered:' + this.registered + ', sender id:' + pda_sender_id);

    //If Device ready , driver > 0 and we are not already registered then do it
    if (this.platformReady.isPlatformReady() && this.pdaParams.driverId > 0 && this.registered == false) {
      // Determine platform e.g. 'Android', 'iOS'
      var platform = this.device.platform;
      if (platform != null) {
        var deviceType = platform.toLowerCase();			// lowercase for loopback installations collection
        var mycurrent_drivernum = this.pdaParams.driverId;
        // initialise the push plugin - phonegap-plugin-push
        this.pushObject = this.push.init(this.pushOptions);

        this.pushObject.on('registration').subscribe((resdata: any) => {
          var token = resdata.registrationId;
          var msg = 'CordovaPushregistered for Driver # : ' + mycurrent_drivernum + ' with token:' + token;
          this.log.info(msg);
          //Init an Object to insert into installation table if our filter below gets empty or filter 
          // returns not the same as the current token just got from Google
          var installation_object = {
            "appId": "osc-push-tpm2",
            "userId": mycurrent_drivernum,
            "deviceToken": token,
            "deviceType": deviceType,
            "pdaVersion": this.pdaParams.getAppVersion()
          };
          var filter = { "where": { "deviceToken": token } };
          this.log.debug(platform + 'CordovaPush.register: check for current registration, filter:' + JSON.stringify(filter));
          this.installationApi.find(filter).subscribe((data: any) => {
            // Try and ensure that there only ever one row for a driver or a token
            // TODO - remove any registrations for this token but NOT for this driver
            // TODO - remove any registrations for this driver but NOT for this token
            // Then register current token for current driver
            if (this.utilService.isEmpty(data) || data === null) {		// no current registration found
              this.log.info(platform + 'CordovaPush.register: no current registration found in installations, saving...');
              //This registers the Google Token with Strongloop and our Database
              this.installationApi.create(installation_object).subscribe(res => {
                // Save data to local storage (it also gets put into $rootScope below)
                localStorage.setItem('osc-push-credentials', JSON.stringify(installation_object));
                //if (!this.sharedService.desktopTesting) {
                  this.storage.set('osc-push-credentials', JSON.stringify(installation_object)).then((res) => {
                    if (res)
                      console.log("osc-push-credentials are set");
                    else
                      console.log("osc-push-credentials is not set");
                  });
                //}

                this.log.info("Sqlite Storage : Setting osc-push-credentials : " + JSON.stringify(installation_object));
                var alertPopup = this.alertCtrl.create({
                  title: 'Device registered',
                  message: token,
                  buttons: ['Ok'],
                  enableBackdropDismiss: false
                });
                alertPopup.present();

              }, (error) => {
                this.log.error(platform + 'CordovaPush.register: failed error:' + error);
              });
            }
            else {

              //Call the createorpatch API
              /* this.installationApi.patchOrCreate(installation_object).subscribe((data)=>{
                this.log.debug("The installationApi.patchOrCreate returned:"+data);
              }); */
              var id = data[0].id;
              this.installationApi.replaceById(id, installation_object).subscribe((result) => {
                this.log.debug("The installationApi.replaceById returned:" + JSON.stringify(result));
              });

              this.log.info(platform + ':CordovaPush.register: current registration already found in installations');
              this.log.debug(platform + ':CordovaPush.register: data:' + JSON.stringify(data));
            }
            this.registered = true;		// we've either just registered or we were already registered
          });
        });


        /*Job Cancel notification*/
        this.pushObject.on('notification').subscribe((notification: any) => {



          this.log.debug(platform + 'CordovaPush:notificationReceived:' + JSON.stringify(notification));
          var payload = notification.additionalData.payload || {};

          /**
           * Riyaz: Store the last 5 UUIDs in the local storage and compare them with the latest UUID.
           * This solves the issue of duplicate execution of notifications.
           */
          //Get the new uuid
          var new_uuid = payload.uuid;
          this.log.debug("New UUID received is:" + new_uuid);
          this.log.debug("Contents of the UUID circular array of length 5 is:" + localStorage.getItem('uuid'));

          var uuid = [];
          //if uuid is null, store new uuid and continue
          if (!localStorage.getItem('uuid')) {
            uuid.push(new_uuid);
            //if (!this.sharedService.desktopTesting) {
              this.storage.set('uuid', JSON.stringify(uuid)).then((res) => {
                if (res)
                  console.log("uuid is set");
                else
                  console.log("uuid is not set");
              });
            //}

            this.log.info("Sqlite Storage : Setting UUID : " + JSON.stringify(uuid));

            localStorage.setItem('uuid', JSON.stringify(uuid));

          }
          else {
            uuid = JSON.parse(localStorage.getItem('uuid'));
            //if new_uuid has already been processed, exit out...
            if (uuid.indexOf(new_uuid) != -1) {
              this.log.debug(" UUIDs are equal, exiting out ..... :");
              return;
            } else {
              //If the circular array of length 5, is full, chip out the oldest element
              if ((JSON.parse(localStorage.getItem('uuid'))).length >= 5)
                uuid.splice(0, 1);

              //Push the new uuid to the circular array
              uuid.push(new_uuid);
              //if (!this.sharedService.desktopTesting) {
                this.storage.set('uuid', JSON.stringify(uuid)).then((res) => {
                  if (res)
                    console.log("uuid is set");
                  else
                    console.log("uuid is not set");
                });
              //}
              this.log.info("Sqlite Storage : Setting UUID : " + JSON.stringify(uuid));
              localStorage.setItem('uuid', JSON.stringify(uuid));

            }
          } /*UUID comparison ends*/


          // If there is a payload type we use that as the broadcast event with the supplied payload
          if (payload.type) {
            // Add platform to payload
            payload.platform = platform;			// event handler may need to know this

            // If a sound has been provided add to payload (normally iOS)
            if (notification.sound)
              payload.sound = notification.sound;

            this.log.debug(platform + 'CordovaPush:about to broadcast:' + payload.type + ':payload:' + JSON.stringify(payload));

            //if notification.additionalData.payload.type="CANCEL", then call next on CANCEL subject.
            if (payload.type === "CANCEL") {
              this.sharedService.CANCEL.next(payload);
            }
            //if notification.additionalData.payload.type="NEWJOB", then call next on NEWJOB subject.
            if (payload.type === "NEWJOB") {
              this.sharedService.NEWJOB.next(payload);
            }

            //if notification.additionalData.payload.type="JOBUPDATE", then call next on JOBUPDATE subject.
            if (payload.type === "JOBUPDATE") {
              this.sharedService.JOBUPDATE.next(payload);
            }

            //if notification.additionalData.payload.type="RESUME", then call next on RESUME subject.
            if (payload.type === "RESUME") {
              this.sharedService.RESUME.next(payload);
            }
            //if notification.additionalData.payload.type="DESTROY", then call next on DESTROY subject.
            if (payload.type === "DESTROY") {
              this.sharedService.DESTROY.next(payload);
            }
            if (payload.type === "messageToDriver") {
              this.sharedService.messageToDriver.next(payload);
            }

            if (payload.type === "commandToPDA") {
              this.sharedService.commandToPDA.next(payload);
            }
          }

          else {
            // no payload type provided simply broadcast a received event with the full notification
            // add platform to notification so we can check in the event handler
            notification.platform = platform;

            this.log.debug(platform + 'CordovaPush:about to broadcast:' + 'pushNotificationReceived');

            // $rootScope.$broadcast('pushNotificationReceived', notification);	// broadcast to the world
            this.sharedService.pushNotificationReceived.next(notification);
          }

        });

        this.pushObject.on('error').subscribe(error =>
          this.log.error(platform + ':CordovaPush :errorOccurred:' + JSON.stringify(error)));
      }// if (platform != null)  ends here
    } //isPlatformReady() ends here
  }///registerForPush() ends here



  getIsRegistered() {
    return this.registered;
  }

}



