import { Injectable } from '@angular/core';

import { BehaviorSubject, Subject } from 'rxjs';


import { Network } from '@ionic-native/network';
import { Platform } from 'ionic-angular';

import { UtilServiceProvider } from '../util-service/util-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { LogServiceProvider } from '../log-service/log-service';
import { SharedServiceProvider } from '../shared-service/shared-service';

/*
  Generated class for the ConnectivityMonitorProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class ConnectivityMonitorProvider {
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'ConnectivityMonitorProvider' };
  log = this.logger.getInstance(this.logParams);
  monitorOnline;
  disconnectSubscription;
  connectSubscription;
  isConnected: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(true);
  networkType: BehaviorSubject<string> = new BehaviorSubject<string>('');

  constructor(private pdaParams: PdaparamsServiceProvider,
    private utilService: UtilServiceProvider, private logger: LogServiceProvider,
    public platform: Platform, private network: Network, private sharedService: SharedServiceProvider) {
    this.log.info('Hello ConnectivityMonitorProvider Provider');
    this.sharedService.networkType = network.type;

    //this.monitorOnline = siteConfig.getSiteConfigValue('PDA_MONITOR_ONLINE') == 'Y';
    //this.monitorOnline = true;

  }

  startWatching = () => {
    console.log("startWatching: ConnectivityMonitorProvider");
    this.log.debug("startWatching");

    this.disConnectSubscribe();
    this.connectSubscribe();

  }//startWatching ends here



  disConnectSubscribe() {
    // watch network for a disconnection
    this.disconnectSubscription = this.network.onDisconnect().subscribe(() => {
      this.log.info('network was disconnected :-(');
      var d = new Date();
      localStorage.setItem("OfflineTimeStamp", this.utilService.getLocalTimestamp(d));
      //alert("Disconnected!!");

      this.sharedService.isOnline = false;
      this.isConnected.next(false);
      this.networkType.next(this.network.type);
    });

  }


  connectSubscribe() {
    // watch network for a connection
    this.connectSubscription = this.network.onConnect().subscribe(() => {
      this.log.info('network connected!');
      //alert("Connected!!");

      if (localStorage.getItem('OfflineTimeStamp')) {
        this.log.info('Device went offline at :' + localStorage.getItem('OfflineTimeStamp'));
        localStorage.removeItem('OfflineTimeStamp');
      }

      //Riyaz - iOS Hack: Set the full replication for syncing to true, when the device gets reconnected.
      this.pdaParams.fullReplication = true;
      
      this.sharedService.isOnline = true;
      this.isConnected.next(true);
      // We just got a connection but we need to wait briefly
      // before we determine the connection type. Might need to wait.
      // prior to doing any api requests as well.
      setTimeout(() => {       
        this.sharedService.networkType = this.network.type;      
        this.networkType.next(this.network.type);
      }, 2000);
    });
  }


  ngOnDestroy() {
    this.disconnectSubscription.unsubscribe();
    this.connectSubscription.unsubscribe();
  }

}



