import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { LoadingController } from 'ionic-angular';


/*
  Generated class for the SharedServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.

  Riyaz: Have a look at https://github.com/NamitaMalik/Realtime-Update-in-Angular2
*/


interface Metadata {
  legid: number,
  baseJob: string,
  legStatus: string
}

interface fileData {
  savedImageURI: string,
  baseFileName: string,
  newFileName: string,
  metadata: Metadata
}
interface pdaPayload {
  command: string,
  params: {};
}
interface driverPayload {
  message: string,
  type: string,
  playsound: boolean,
  platform: string,
  sound: string
}
interface Payload {
  data: {
    type: string,
    baseJob: string,
    bookingDay: number
  },
  platform: string,
  playsound: boolean,
  payload: Payload,
  type: string,
  sound: string
}
interface Message {
  type: string,
  baseJob: string,
  bookingDay: number
}

interface additionalData {
  payload: Payload,
  message: string

}

interface notification {
  platform: string,
  event: string,
  message: Message,
  title: string,
  count: number,
  image: string,
  additionalData: additionalData,
  payload: Payload,
  alert: string,
  sound: string,
  type: string
}

@Injectable()
export class SharedServiceProvider {

  //Riyaz: Events are implemented using Subjects & BehaviorSubjects
  SITE_CONFIG_LOADED = new Subject<boolean>();
  SODSERVICE_IS_NEW_DAY = new Subject<boolean>();

  APP_ON_LINE: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  APP_OFF_LINE: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

  commandToPDA: Subject<pdaPayload> = new Subject<pdaPayload>();
  messageToDriver: Subject<driverPayload> = new Subject<driverPayload>();
  pushNotificationReceived: Subject<notification> = new Subject<notification>();

  jobMetaDataLoadedSubject = new Subject<Boolean>();
  tpmPdaCtlsLoadedSubject = new Subject<Boolean>();
  PAUSE = new Subject<Boolean>();
  RESUME = new Subject<Boolean>();
  DESTROY = new Subject<Payload>();

  NEWJOB = new Subject<Payload>();
  JOBUPDATE = new Subject<Payload>();
  CANCEL = new Subject<Payload>();
  imageSaved = new Subject<fileData>();
  imageRefresh = new Subject<Boolean>();

  loginFlag;
  testing: boolean = false;
  isOnline: boolean = false;
  isDbOpen : boolean =false;
  desktopTesting:boolean = false;
  deliverToBase:boolean = false;
  seqno = 0;
  tab = "";
  isAppinBackground: boolean = false;
  isDiagnosticsRunning: boolean = false;
  newMsgCount = 0;
  temp = [];
  networkType;
  driverId: number;
  tabLoading;
  multidel_phototaken = false;
  localStorageZapped;
  

  //Riyaz: Global variables are implemented using getter & setter methods
  //See https://ionicallyspeaking.com/2016/03/10/global-variables-in-ionic-2/

  props = new Map();


  constructor(public loadingCtrl: LoadingController) {
    this.setMyConfiguredForJsea(false);
    this.tabLoading = this.loadingCtrl.create({});
    //console.log('Hello SharedServiceProvider Provider');
  }

  setJobId(value) {
    this.props.set('jobid', value);
  }

  getJobId() {
    return this.props.get('jobid');
  }

  setJobMetaData(value) {
    //console.log("3.5 Jobmetadata in shared-service.ts set to==" + value);
    localStorage.setItem('jobMetadata', '');
    localStorage.setItem('jobMetadata', JSON.stringify(value));
    //alert("Jobmetadata set!!");
  }

  getJobMetaData() {
    return JSON.parse(localStorage.getItem('jobMetadata'));
  }

  setHistoryMetaData(value) {

    this.props.set('historyMetadata', value);
  }

  getHistoryMetadata() {
    return this.props.get('historyMetadata');
  }

  setBNewItem(value) {
    this.props.set('bNewItem', value);
  }

  getBNewItem() {
    return this.props.get('bNewItem');
  }

  setDesktopBrowserScrolling(value) {
    this.props.set('desktopBrowserScrolling', value);
  }

  getDesktopBrowserScrolling() {
    return this.props.get('desktopBrowserScrolling');
  }

  setSyncInProgress(value) {

    this.props.set('syncInProgress', value);
  }

  getSyncInProgress() {

    return this.props.get('syncInProgress');
  }

  setFirstTimeLoad(value) {
    this.props.set('firstTimeLoad', value);
  }

  getFirstTimeLoad() {
    return this.props.get('firstTimeLoad');
  }

  setMyConfiguredForJsea(value) {
    this.props.set('myConfiguredForJsea', value);
  }

  getMyConfiguredForJsea() {
    return this.props.get('myConfiguredForJsea');
  }


  setCancelNotif(value) {
    this.props.set('CancelNotif', value);
  }

  getCancelNotif() {
    return this.props.get('CancelNotif');
  }

  showSpinner() {
    this.tabLoading = this.loadingCtrl.create({
      cssClass: 'transparent'
    });

    this.tabLoading.present().then(() => {
      this.tabLoading.dismiss();
    });
  }

}






