import { Observer } from 'rxjs/Observer';
import { Observable } from 'rxjs/Observable';
import { Injectable } from '@angular/core';

import { SiteConfigApi } from '../../../shared/sdk/services';

import { SharedServiceProvider } from '../shared-service/shared-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { LogServiceProvider } from '../log-service/log-service';
import { LoopBackConfig } from '../../../shared/sdk';
import { CustomerconnServiceProvider } from '../customerconn-service/customerconn-service';


/*
  Generated class for the SiteconfigServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class SiteconfigServiceProvider {

  keylist = [
    'PDA_BARCODE_IS_JOBNUM',
    'PDA_BARCODES',
    'PDA_JSEA_ON',
    'PDA_JSEA_TYPE',
    'PDA_PICKUP_ALL',
    'PDA_DEPART_ALL',
    'PDA_DELIVER_ALL',
    'PDA_FULL_STATUSES',
    'PDA_IMAGES',
    'PDA_IMAGES_URL',
    'PDA_NOTES',
    'PDA_DEL_DAYSBACK',
    'PDA_SORT_COL1',
    'PDA_DISPLAY_DATE',
    'PDA_SIGNATURE_PUP',
    'PDA_SIGNATURE_DPUP',
    'PDA_DISCRETE_DEL_SIGNAT',
    'PDA_MULTIDEL_NOTE_TEXT',
    'PDA_DISPLAY_FROM',
    'PDA_DISPLAY_TO',
    'PDA_ROW1_FONT_SIZE',
    'PDA_MONITOR_ONLINE',
    'PDA_CLIENT_CONFLICTS',
    'PDA_MANDATORY_PHOTOS',
    'PDA_IMAGE_NOTES',
    'PDA_NOTIFY_REPEAT',
    'PDA_NOTIFY_INTERVAL',
    'PDA_SENDER_ID',
    'PDA_REQ_NAVIGATION',
    'PDA_SHOW_CLIENT_NAME',
    'PDA_JSEA_BOTH_YN',
    'PDA_MANDATORY_NOTES',
    'PDA_ATTACHED_DOCS',
    'PDA_JOB_ROUTING',
    'PDA_ROUTE_MAP',
    'PDA_DISABLE_BACKGROUND_SYNC',
    'PDA_DISABLE_DEVICE_DIAGNOSTICS',
    'PDA_DISABLE_DIAGNOSTICS_BGPS_ON_LOGOFF',
    'PDA_JSEA_NO_NO',
    'PDA_DELIVERY_TO_BASE',
		'PDA_BREAK_TIMES'
  ];

  siteconfigs = null;
  injector;
  userId;
  connDetails;
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'SiteconfigServiceProvider' };
  log = this.logger.getInstance(this.logParams);

  constructor(private pdaParams: PdaparamsServiceProvider,
    private siteConfigApi: SiteConfigApi,
    private sharedService: SharedServiceProvider,
    private logger: LogServiceProvider, private customerConn: CustomerconnServiceProvider
  ) {
    console.log('Hello SiteconfigServiceProvider Provider');
  }



  loadAllConfigsFromServer() {
    var filter = {
      "where":
      {
        and: [{ "confActive": true }, { "confKey": { "inq": this.keylist } }]
      }
    };

    this.userId = localStorage.getItem('userId');
    this.connDetails = this.customerConn.getConnectionDetails(this.userId.toLowerCase());
    LoopBackConfig.setBaseURL((this.connDetails.url));
    LoopBackConfig.setApiVersion('api');

    return Observable.create((observer: Observer<string>) => {
      this.siteConfigApi.find(filter).subscribe(siteconfigs => {
        this.log.info("Retrieved SiteConfigs from DB:" + JSON.stringify(siteconfigs));

        this.siteconfigs = siteconfigs;
        var len = this.siteconfigs.length;
        if (len == 0) {		// not found
          this.log.error("No site config values found");
          //console.log("No site config values found");
        } else {

          for (var i = 0; i < len; i++) {
            // store in local storage for future use
            // TODO - store in individual keys or as a site_config key for an object with all keys?
            localStorage.setItem(this.siteconfigs[i].confKey, this.siteconfigs[i].confValue);
          }
          try {
            localStorage.setItem('SITE_CONFIG_LOADED', 'true');
          } catch (domException) {
            this.log.error("Unable to store items in local storage::" + JSON.stringify(domException));
          }
          this.sharedService.SITE_CONFIG_LOADED.next(true);
        }
        observer.next("success");
      },
        error => {
          this.log.error(error);
          Observable.throw(error);
        });
    });
  } //End of  getAllConfigsFromServer() 



  deleteLocalConfigs() {
    // Remove all config key/value pairs from local storage
    // NOTE this should in general ALWAYS be followed by a getAllConfigsFromServer() call to repopulate
    var len = this.keylist.length;
    for (var i = 0; i < len; i++) {
      if (localStorage.getItem(this.keylist[i]) !== null) {
        localStorage.removeItem(this.keylist[i]);
      }
    }

    this.sharedService.SITE_CONFIG_LOADED.next(false);
  }

  // returns a promise as we are using it in app.module.js to resolve routes
  getSiteConfigYN(key) {
    var retval = localStorage.getItem(key);
    if (!retval) {
      retval = 'N';
    }
    return retval;
  }


  getSiteConfig(key) {
    var retval = localStorage.getItem(key);
    if (!retval) {
      retval = '';
    }
    return Observable.of(retval);
  }

  getSiteConfigInt(key) {
    var retval = +localStorage.getItem(key);		// the "+" causes a cast to an integer
    if (!retval) {
      retval = 0;
    }
    return retval;
  }

  // no promise on this one so it can be used in inline synchronous code
  getSiteConfigValue(key) {
    var retval = localStorage.getItem(key);
    if (!retval) {
      retval = '';
    }
    return (retval);
  }

}



