import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { LogServiceProvider } from '../../providers/log-service/log-service';

/*
  Generated class for the SqliteServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SqliteServiceProvider {

  private db = null;
  private dbname = 'tplus.db';
  // (<any>window).
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'SqliteServiceProvider' };
  log = this.logger.getInstance(this.logParams);

  constructor(public http: HttpClient, private pdaParams: PdaparamsServiceProvider, private logger: LogServiceProvider) {
    this.log.info('Hello SqliteServiceProvider Provider');
    /*  if (!this.isOpen) {
       document.addEventListener('deviceready', this.opendbOnDeviceReady, false);
     } */
  }//constructor ends

  opendbOnDeviceReady = () => {
    //this.db = (<any>window).sqlitePlugin.openDatabase({ name: 'test.db', location: 'default' });
    return new Promise((resolve, reject) => {
      this.db = (<any>window).sqlitePlugin.openDatabase({ name: this.dbname, location: 'default' }, (db) => {
        // Here, you might create or open the table.
        db.transaction((tx) => {
          // ...
          tx.executeSql('CREATE TABLE IF NOT EXISTS tplusStorage (item,itemvalue)', (tx, res) => {
            this.log.info("tplusStorage table created -- " + res);
            resolve(true);
          });
        }, (error) => {
          this.log.error('Create transaction error: ' + error.message);
          resolve(false);
        }, () => {
          this.log.info('Create transaction ok');
          resolve(true);
        });
      }, (error) => {
        this.log.error('Open database ERROR: ' + JSON.stringify(error));
        resolve(false);
      });
    });
  }



  set = (item, itemvalue) => {
    //  let itemvalue = JSON.stringify(itemval);
    if (this.db == null || this.db === 'undefined') {
      this.log.error("set: Db is undefined.");
      return;
    }

    return new Promise((resolve, reject) => {
      this.db.transaction((tx) => {

        var query = "INSERT OR REPLACE INTO tplusStorage (item,itemvalue) VALUES (?,?)";

        tx.executeSql(query, [item, itemvalue], (tx, res) => {
          // this.log.info("Sqlite Storage : Inserted item is :" + item + " for itemvalue:" + itemvalue);
          //this.log.info("insertId: " + res.insertId + " -- probably 1");
          this.log.info("INSERT OR REPLACE rowsAffected: " + res.rowsAffected + " -- should be 1");
          resolve(true);
        },
          (tx, error) => {
            this.log.error('INSERT | UPDATE error: ' + error.message);
            resolve(false);
          });
      }, (error) => {
        this.log.error('Insert | update transaction error: ' + error.message);
        resolve(false);
      }, () => {
        this.log.info('Insert | update transaction ok');
        resolve(true);
      });

    });
  }

  get = (item) => {
    if (this.db == null || this.db === 'undefined') {
      this.log.error("get: Db is undefined.");
      return;
    }
    var res;

    return new Promise((resolve, reject) => {
      this.db.transaction((tx) => {
        var query = "SELECT itemvalue FROM tplusStorage WHERE item = ?";
        tx.executeSql(query, [item], (tx, resultSet) => {
          this.log.info('Select query item is :' + item);
          if (resultSet.rows.length > 0 && resultSet.rows.item(0).itemvalue) {
            res = resultSet.rows.item(0).itemvalue;
            resolve(res);
          }
          else {
            res = 0;
            resolve(res);
          }
        },
          (tx, error) => {
            //reject(error);

            this.log.error('SELECT error: ' + error.message);
            resolve(0);
          });
      }, (error) => {
        //reject(false);
        this.log.error('Select transaction error: ' + error.message);
        resolve(0);
      }, () => {
        this.log.info('Select transaction ok');
        resolve(res);
      });
    });
  }

  getAndSetToLocalStorage = () => {
    if (this.db == null || this.db === 'undefined') {
      this.log.error("getAndSetToLocalStorage: Db is undefined.");
      return;
    }
    return new Promise((resolve, reject) => {
      this.db.transaction((tx) => {
        var query = "SELECT item,itemvalue FROM tplusStorage";
        tx.executeSql(query, [], (tx, resultSet) => {
          for (var x = 0; x < resultSet.rows.length; x++) {
            this.log.info("item: " + resultSet.rows.item(x).item + ", ItemValue: " + resultSet.rows.item(x).itemvalue);
            localStorage.setItem(resultSet.rows.item(x).item, resultSet.rows.item(x).itemvalue);

            if (resultSet.rows.item(x).item == 'driverId') {
              this.pdaParams.setDriverInfo(resultSet.rows.item(x).itemvalue);
              this.pdaParams.logoffDriver();
            }
          }
          resolve(true);
        },
          (tx, error) => {
            this.log.error('SELECT error: ' + error.message);
            resolve(false);
          });
      }, (error) => {
        this.log.error('Select transaction error: ' + error.message);
        resolve(false);
      }, () => {
        this.log.info('getAndSetToLocalStorage transaction ok');
        resolve(true);
      });
    });
  }



  clear = () => {
    if (this.db == null || this.db === 'undefined') {
      this.log.error("clear: Db is undefined.");
      return;
    }
    return new Promise((resolve, reject) => {
      this.db.transaction((tx) => {
        var query = "DELETE FROM tplusStorage";
        tx.executeSql(query, [], (tx, res) => {
          this.log.info("rowsAffected: " + res.rowsAffected);
          resolve(true);
        },
          (tx, error) => {
            reject(error);
            this.log.error('DELETE error: ' + error.message);
          });
      }, (error) => {
        resolve(false);
        this.log.error('Delete transaction error: ' + error.message);
      }, () => {
        this.log.info('Delete transaction ok');
      });
    });
  }


  printAll = () => {
    this.db.transaction((tx) => {
      var query = "SELECT item, itemvalue FROM tplusStorage";
      tx.executeSql(query, [], (tx, resultSet) => {
        this.log.info("----------START PRINTING VALUES---------------");
        for (var x = 0; x < resultSet.rows.length; x++) {
          this.log.info("Item: " + resultSet.rows.item(x).item +
            ", Value: " + resultSet.rows.item(x).itemvalue);
          this.log.info("-------------------------");
        }
        this.log.info("---------END PRINTING VALUES----------------");
      },
        (tx, error) => {
          this.log.error('printAll error: ' + error.message);
        });
    }, (error) => {
      //reject(false);
      this.log.error('PRINTALL transaction error: ' + error.message);
    }, () => {
      this.log.info('PRINTALL transaction ok');
    });
  }


  sqliteStorageList = () => {
    return new Promise((resolve, reject) => {
      this.db.transaction((tx) => {
        var query = "SELECT item,itemvalue FROM tplusStorage";
        tx.executeSql(query, [], (tx, resultSet) => {
          resolve(resultSet.rows.item);
        },
          (tx, error) => {
            //reject(error);
            resolve(false);
            //this.log.info('SELECT error: ' + error.message);
          });
      }, (error) => {
        reject(error);
        //reject(false);
        //this.log.info('Select transaction error: ' + error.message);
      }, () => {
        this.log.info('getAndSetToLocalStorage transaction ok');
      });
    });

  }

  ////////////////////////////////////////////////////////////////////////

  echoTest = () => {
    document.addEventListener('deviceready', () => {
      (<any>window).sqlitePlugin.echoTest(() => {
        this.log.info('ECHO test OK');
      });
    });
  }
  selfTest = () => {
    document.addEventListener('deviceready', () => {
      (<any>window).sqlitePlugin.selfTest(() => {
        this.log.info('SELF test OK');
      });
    });
  }
  sqlStringTest = () => {
    document.addEventListener('deviceready', () => {
      var db = (<any>window).sqlitePlugin.openDatabase({ name: this.dbname, location: 'default' });
      db.transaction((tr) => {
        tr.executeSql("SELECT upper('Test string') AS upperString", [], (tr, rs) => {
          this.log.info('Got upperString result: ' + rs.rows.item(0).upperString);
        });
      });

    });
  }//sqlStringTest ends


  closeDB = () => {
    this.db.close(() => {
      this.log.info("DB closed!");
    }, (error) => {
      this.log.error("Error closing DB:" + error.message);
    });
  }

}



