import { Injectable } from '@angular/core';
import { Device } from '@ionic-native/device';
import { Platform } from 'ionic-angular';
import { Insomnia } from '@ionic-native/insomnia';
import { BackgroundMode } from '@ionic-native/background-mode';

import { LogServiceProvider } from '../log-service/log-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { BackgroundGeolocationServiceProvider } from '../background-geolocation-service/background-geolocation-service';
import { SharedServiceProvider } from '../shared-service/shared-service';
import { SiteconfigServiceProvider } from '../siteconfig-service/siteconfig-service';

/*
  Generated class for the PlatformReadyServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/


@Injectable()
export class PlatformReadyServiceProvider {

  logOb;
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'PlatformReadyServiceProvider' };
  log = this.logger.getInstance(this.logParams);
  isready: boolean = false;
  bgenabled = false;
  pda_disable_diagnostics_bgps_on_logoff;

  constructor(private device: Device, private logger: LogServiceProvider,
    private pdaParams: PdaparamsServiceProvider, public platform: Platform,
    private insomnia: Insomnia, private backgroundGeolocationService: BackgroundGeolocationServiceProvider,
    private sharedService: SharedServiceProvider, private backgroundMode: BackgroundMode,
    private siteConfig: SiteconfigServiceProvider) {
    console.log('constructed');
    this.log.debug('constructed');
    this.pda_disable_diagnostics_bgps_on_logoff = (this.pdaParams.pda_disable_diagnostics_bgps_on_logoff || (this.siteConfig.getSiteConfigValue('PDA_DISABLE_DIAGNOSTICS_BGPS_ON_LOGOFF') == 'Y'));
    this.platform.ready().then((readySource) => {

      var osplatform = this.device.platform;

		console.log('platform ready from:' + readySource + ', osplatform:' + osplatform);
		this.log.debug('platform ready from:' + readySource + ', osplatform:' + osplatform);

      if (osplatform != null) {

        this.log.debug('Platform ready from :' + readySource);
        // Platform now ready, execute any required native code
        var model = this.device.model;
        //var platforms = this.platform.platforms();
        var version = this.device.version;
        var manufacturer = this.device.manufacturer

        if (this.platform.is('cordova')) {
          this.isready = true;
        }

        //this.isready = false;
        this.log.info("Device Model:" + model + ", Manufacturer:" + manufacturer + ", platform:" + osplatform + ", OS ver:" + version);

        if (model == 'CipherLab RS30') {
          this.log.debug("Device is a CipherLab RS30 " + model);
        }
        else {
          this.log.debug("Device is not a CipherLab RS30 HEAD scanning NOT Available: " + model);
        }

        // Keep screen on for our app
        this.insomnia.keepAwake();

        this.backgroundMode.on("enable").subscribe(() => {
          this.log.debug("BGMODE:::BGMODE ENABLED");
        });

        this.backgroundMode.on("deactivate").subscribe(() => {
          this.log.debug("BGMODE:::BGMODE DEACTIVATED");
          this.bgenabled = false;
        });

        this.backgroundMode.on("failure").subscribe(() => {
          this.log.debug("BGMODE:::BGMODE FAILED");
        });

        this.backgroundMode.on("activate").subscribe(() => {
          this.log.debug("BGMODE:::BGMODE ACTIVATED WITH BGENABLED FLAG AS :" + this.bgenabled);
          if (this.bgenabled == false) {
            this.backgroundMode.disableWebViewOptimizations();
            this.log.debug("BGMODE:::BG MODE ON EVENT FIRED !! DISABLEWEBVIEWOPTIMIZATIONS() CALLED!!");
            this.bgenabled = true;
          }
        }
          ,
          (err) => {
            if (err.error === 'cordova_not_available' || err.error === 'plugin_not_available') {
              this.log.debug('activate:plugin not install');
            }
          });

        this.backgroundMode.on("disable").subscribe(() => {
          this.log.debug("BGMODE:::BGMODE DISABLED");
          this.bgenabled = false;
        }, (err) => {
          this.log.debug('BG MODE DISABLE: ERR = ' + err);
        });

        this.backgroundMode.enable();
        this.backgroundMode.setDefaults({
          hidden: true, title: 'App is running in the background', text: 'Doing Background GPS...'
        });

        this.platform.pause.subscribe(() => {

          this.pda_disable_diagnostics_bgps_on_logoff = (this.pdaParams.pda_disable_diagnostics_bgps_on_logoff || (this.siteConfig.getSiteConfigValue('PDA_DISABLE_DIAGNOSTICS_BGPS_ON_LOGOFF') == 'Y'));

          this.log.info("platform.pause: Inside this.platform.pause.subscribe() ");
          if (!this.sharedService.loginFlag && this.pda_disable_diagnostics_bgps_on_logoff)
            this.log.info("PAUSE event fired: Background GPS will not fire because pda_disable_diagnostics_bgps_on_logoff is set to TRUE.");
          else {
            this.log.info("PAUSE event fired: Background GPS about to START.");
            this.backgroundGeolocationService.start();
          }

          this.sharedService.isAppinBackground = true;
          this.sharedService.PAUSE.next(true);
        });

        this.platform.resume.subscribe(() => {
          this.log.info("RESUME event fired: Background GPS about to STOP.");
          this.backgroundGeolocationService.stop();
          this.sharedService.isAppinBackground = false;
          this.sharedService.RESUME.next(true);
        });
      }
    });

  }//constructor ends;


  isPlatformReady() {
    return this.isready;
  }


}



