import { Injectable } from '@angular/core';
import { AlertController } from 'ionic-angular';

import { Observable } from 'rxjs/Observable';

import { LogServiceProvider } from '../log-service/log-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { SiteconfigServiceProvider } from '../siteconfig-service/siteconfig-service';
import { SharedServiceProvider } from '../shared-service/shared-service';
import { JseaServiceProvider } from '../jsea-service/jsea-service';
import { MessageServiceProvider } from '../message-service/message-service';
import { EventServiceProvider } from '../event-service/event-service';
import { JobServiceProvider } from '../job-service/job-service';
import { BackgroundGeolocationServiceProvider } from '../background-geolocation-service/background-geolocation-service';
import { PlatformReadyServiceProvider } from '../platform-ready-service/platform-ready-service';
import { DeviceDiagnosticServiceProvider } from '../device-diagnostic-service/device-diagnostic-service';

/*
  Generated class for the SodServiceProvider provider.

  Start of Day Service

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/

@Injectable()
export class SodServiceProvider {
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'SodServiceProvider' };
  log = this.logger.getInstance(this.logParams);
  d;
  now;
  type;
  //When App starts Get from localstorage
  //Logic is everytime the app resumes we check the resume datetime
  // and if its a new day the update the time in local storage
  // and do a logoff and clear local storage
  today = new Date();
  mycurday = this.today.getDate();
  pda_disable_diagnostics_bgps_on_logoff;


  constructor(private jobService: JobServiceProvider,
    private pdaParams: PdaparamsServiceProvider, private siteConfig: SiteconfigServiceProvider,
    private logger: LogServiceProvider, private sharedService: SharedServiceProvider,
    private jseaService: JseaServiceProvider, private messageService: MessageServiceProvider,
    private alertCtrl: AlertController, private eventService: EventServiceProvider,
    private platformReady: PlatformReadyServiceProvider, private backgroundGeolocationService: BackgroundGeolocationServiceProvider,
    public deviceDiagnosticServiceProvider: DeviceDiagnosticServiceProvider) {
    //console.log('Hello SodServiceProvider Provider');
    this.sharedService.RESUME.subscribe(val => {
      this.log.debug('RESUME detected');
      this.checkDoSodAction('FIRST_RESUME');
    });

  }

  //this.log.debug('Instantiating mycurday = :' + mycurday);

  checkDoSodAction(type) {
    this.pda_disable_diagnostics_bgps_on_logoff = (this.pdaParams.pda_disable_diagnostics_bgps_on_logoff || (this.siteConfig.getSiteConfigValue('PDA_DISABLE_DIAGNOSTICS_BGPS_ON_LOGOFF') == 'Y'));

    //Get What is stored from last initial resum on a day in past
    var prevResumeDateStr = localStorage.getItem('FIRST_RESUME_DATE');

    if (type == 'FIRST_RESUME') {
      this.today = new Date();
      this.mycurday = this.today.getDate();

      if (prevResumeDateStr) {
        var prevResumeDate = new Date(prevResumeDateStr);

        var prevResumeDay = prevResumeDate.getDate();
        this.log.debug('On Resume prevResumeDate from localstorage = :' + prevResumeDateStr + ' Str convertTodate = ' + prevResumeDate + ' prevResumeDay = ' + prevResumeDay + ' mycurday = ' + this.mycurday);

        this.log.debug('pda_disable_diagnostics_bgps_on_logoff:'+this.pda_disable_diagnostics_bgps_on_logoff);

        //Resume on different day
        //if( prevResumeDay == mycurday )
        if (prevResumeDay != this.mycurday) {
          //Do some shit and store
          this.now = new Date();
          localStorage.setItem('FIRST_RESUME_DATE', this.now);
          this.pdaParams.logoffDriver();

          if (this.platformReady.isPlatformReady() && this.pda_disable_diagnostics_bgps_on_logoff) {
            this.backgroundGeolocationService.stop();
            this.deviceDiagnosticServiceProvider.stopDiagnostics();
          }

          this.eventService.sendMsg('LOGOFF', '');
          this.log.debug(" prevResumeDay != mycurday mycurday = " + this.mycurday);

          //Set the routed & map jobs to null
          localStorage.setItem('routedJobs', '');
          localStorage.setItem('mapJobs', '');

          //$rootScope.$broadcast('SODSERVICE_IS_NEW_DAY');
          this.sharedService.SODSERVICE_IS_NEW_DAY.next(true);
          localStorage.setItem('SODSERVICE_IS_NEW_DAY', 'true');

          //Dump Device Info
          this.messageService.dumpDeviceInfo();

          // Get site config parameters from server
          var result$: Observable<string> = this.siteConfig
            .loadAllConfigsFromServer()
            .subscribe((result) => {
              //console.log("Site Config Reloaded!");
              this.log.info("Site Config Reloaded for prevResumeDateStr=true from sod-service.ts --> checkDoSodAction()");
              this.jobService.deleteOldJobs('');
              this.jseaService.deleteOldData();
              // NOTE - should really be done only after we've deleted data
              // so perhaps in a callback wrapper?
              this.messageService.clearChangeData();
              this.messageService.dumpLocalStorage();
            },
              (error) => {
                this.showError("Unable to load Site Configs.Please check your connection.");
                this.messageService.dumpLocalStorage();
                return;
              });
        }
        else {
          this.log.debug('Not new date');

					// LT - to ensure background mode processes run correctly after app start
					// we need to start the services
					if (this.platformReady.isPlatformReady()) {
							//this.backgroundGeolocationService.start();
							if(!this.sharedService.isDiagnosticsRunning)
									this.deviceDiagnosticServiceProvider.startWatching();
					}

         // this.messageService.dumpLocalStorage();
          //Dump Device Info
          this.messageService.dumpDeviceInfo();
        }
      }
      else //Never Stored 
      {
        this.now = new Date();
        localStorage.setItem('FIRST_RESUME_DATE', this.now);
        this.log.debug('On Resume prevResumeDate is NULL date = ' + this.now + ' Will clearChange Data');

        //Riyaz: No need to logoff the driver, if he logs in for the first time.The config data is loaded dynamically
        //this.pdaParams.logoffDriver();
        //this.eventService.sendMsg('LOGOFF', '');

        this.messageService.clearChangeData();

        //Dump Device Info
        this.messageService.dumpDeviceInfo();

        // Get site config parameters from server
        var result$: Observable<string> = this.siteConfig
          .loadAllConfigsFromServer()
          .subscribe((result) => {
            //console.log("Site Config Reloaded!");
            this.log.info("Site Config Reloaded for prevResumeDateStr=false from sod-service.ts --> checkDoSodAction()");
            this.messageService.dumpLocalStorage();
          },
            (error) => {
              this.showError("Unable to load Site Configs.Please check your connection.");
              this.messageService.dumpLocalStorage();
              return;
            });
      }

      // TODO - maybe get site config parameters from server here?
    }
    else {
      this.log.error('unknown type:' + type);
    }
  }//checkDoSodAction(type) ends here

  showError(text) {
    //this.loading.dismiss();

    let alert = this.alertCtrl.create({
      title: 'Error',
      subTitle: text,
      buttons: ['OK'],
      enableBackdropDismiss: false
    });
    alert.present();
  }

  clearSODDate() {
    // This will force an initial start of day
    localStorage.removeItem('FIRST_RESUME_DATE');
  }

  setSODDate() {
    // This will force a start of day by setting date to yesterday
    this.d = new Date();
    this.d.setDate(this.d.getDate() - 1);
    localStorage.setItem('FIRST_RESUME_DATE', this.d);
  }

  //This will happen the first time they resume the app on a day

  //For testing, however JobsIndexCtrl wont see this as this service is initialised before JObsIndexCtrl is

}



