import { Injectable } from '@angular/core';


declare const lbclient: any;
interface optionsInterface {
  filter: string;
  lastFilter: string;
};
interface sinceInterface {
  push: number;
  pull: number;
}

/*
  Generated class for the JobnoteReplicationServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class JobnoteReplicationServiceProvider {

  LocalNote = lbclient.models.LocalNote;
  RemoteNote = lbclient.models.RemoteNote;
  models = lbclient.models;
  lastFilter = {};
  d = new Date();
  isOnline;
  since: sinceInterface = { push: -1, pull: -1 };

  constructor() {
    //console.log('Hello JobnoteReplicationServiceProvider Provider');

    // LT - don't work with our current loopback version - use observe ?  Maybe need filter (and callback?)
    this.LocalNote.on('before save', function (ctx, next) {
      next();
    });

    // sync local changes if connected
    this.LocalNote.on('after save', function (ctx, next) {
      next();
      this.job_note_sync();
    });
    this.LocalNote.on('after delete', function (ctx, next) {
      next();
      this.job_note_sync();
    });

  }

  job_note_sync = (callback, filter) => {

    var options: optionsInterface = { filter: '', lastFilter: '' };
    var cb = null;
    cb = callback;
    options.filter = filter;
    options.lastFilter = filter;
    this.lastFilter = filter;
    var counter = 0;

    //console.log(new Date().toISOString() + ': starting replication');

    // LT - 10/12/2015 - original code - does remote first
    this.LocalNote.replicate(
      this.since.push,
      this.RemoteNote,
      options,
      (err, conflicts, cps) => {
        this.since.push = cps;
        this.LocalNote.replicate(
          this.since.push,
          this.RemoteNote,
          options,
          (err, conflicts, cps) => {
            this.since.pull = cps;
            //console.log(new Date().toISOString() + ': finished replication');
            cb && cb(err, conflicts);
          });
      });
  }
}



