import { Injectable } from '@angular/core';

import { LogServiceProvider } from '../log-service/log-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { JobReplicationServiceProvider } from '../job-replication-service/job-replication-service';
import { SiteconfigServiceProvider } from '../siteconfig-service/siteconfig-service';
import { SharedServiceProvider } from '../shared-service/shared-service'
/*
  Generated class for the SyncServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class SyncServiceProvider {


  mystr;
  lcallingfunc;
  isSyncing: boolean = false;
  pda_disable_background_sync;

  //sync = lbclient.sync(this.arg1,this.arg2);

  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'SyncServiceProvider' };
  log = this.logger.getInstance(this.logParams);

  constructor(private logger: LogServiceProvider, private siteConfig: SiteconfigServiceProvider,
    private pdaParams: PdaparamsServiceProvider, private sharedService: SharedServiceProvider,
    public jobReplicationService: JobReplicationServiceProvider) {
    //console.log('Hello SyncServiceProvider Provider');
    this.pda_disable_background_sync = (this.pdaParams.pda_disable_background_sync || (this.siteConfig.getSiteConfigValue('PDA_DISABLE_BACKGROUND_SYNC') == 'Y'));
  }


  hybridSync = (callback, filter) => {


    //this.mystr = 'syncService: About to sync SyncInprogress = ' + this.isSyncing + " Called from (last value set =) [" + this.lcallingfunc + "]";
    //this.log.debug(this.mystr);

    //  if (!this.isSyncing) {
    if (this.sharedService.getSyncInProgress() == false) {     // this.isSyncing = true;

      //this.mystr = 'syncService: isSyncInprogress is off so turn it on before Replication sync call ';
      //this.log.debug(this.mystr);


      this.pda_disable_background_sync = (this.pdaParams.pda_disable_background_sync || (this.siteConfig.getSiteConfigValue('PDA_DISABLE_BACKGROUND_SYNC') == 'Y'));

      //If the app is in the background and pda_disable_background_sync is true, do not sync
      if (this.pda_disable_background_sync && this.sharedService.isAppinBackground)
        this.log.info("Since app is the background and pda_disable_background_sync is set to true, replication code will NOT be executed.");
      else {

        this.sharedService.setSyncInProgress(true);
        //var startTime: number = new Date().getTime();
        //this.log.debug("============SYNC: STARTED AT ============" + startTime);
        this.log.debug("============SYNC: STARTED============");
        //alert("SYNC STARTED");
        this.jobReplicationService.sync(callback, filter);
        //this.log.debug("============SYNC: FINISHED============");
        //this.sharedService.setSyncInProgress(false);
        //var endTime: number = new Date().getTime();
        //this.log.debug("============SYNC: FINISHED AT ============:" + endTime);        
        //this.log.debug("============SYNC: TOOK ============:" + (endTime - startTime) + " ms");
      }

    }
  }

  getSyncInProgress = () => {

    this.mystr = 'syncService: getSyncInProgress:' + this.isSyncing;
    this.log.debug(this.mystr);

    return this.isSyncing;
  };

  setCallingFunc = (callingfunc) => {

    this.mystr = 'syncService: In setCallingFunc value = ' + callingfunc;
    this.log.debug(this.mystr);

    this.lcallingfunc = callingfunc;
  };

  setSyncInProgress = (torf) => {

    this.isSyncing = torf;
    this.mystr = 'syncService: In setSyncInprogress setting value = ' + this.isSyncing;
    this.log.debug(this.mystr);
  };



}



