import { Injectable } from '@angular/core';


declare const lbclient: any;
interface optionsInterface {
  filter: string;
  lastFilter: string;
};
interface sinceInterface {
  push: number;
  pull: number;
}
/*
  Generated class for the BarcodeReplicationServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class BarcodeReplicationServiceProvider {

  LocalBarcodeHist = lbclient.models.LocalBarcodeHist;
  RemoteBarcodeHist = lbclient.models.RemoteBarcodeHist;
  models = lbclient.models;
  lastFilter = {};
  d = new Date();
  isOnline;
  since: sinceInterface = { push: -1, pull: -1 };


  constructor() {
    //console.log('Hello BarcodeReplicationServiceProvider Provider');

    this.LocalBarcodeHist.on('before save', function (ctx, next) {
      next();
    });

    // sync local changes if connected
    this.LocalBarcodeHist.on('after save', function (ctx, next) {
      next();
      this.barcode_sync();
    });
    this.LocalBarcodeHist.on('after delete', function (ctx, next) {
      next();
      this.barcode_sync();
    });
  }

  barcode_sync = (callback, filter) => {

    var options: optionsInterface = { filter: '', lastFilter: '' };
    var cb = null;
    cb = callback;
    options.filter = filter;
    options.lastFilter = filter;
    this.lastFilter = filter;

    var counter = 0;

    //console.log(new Date().toISOString() + ': starting replication');

    // LT - 10/12/2015 - original code - does remote first
    this.LocalBarcodeHist.replicate(
      this.since.push,
      this.RemoteBarcodeHist,
      options,
      (err, conflicts, cps) => {
        this.since.push = cps;
        this.LocalBarcodeHist.replicate(
          this.since.push,
          this.RemoteBarcodeHist,
          options,
          (err, conflicts, cps) => {
            this.since.pull = cps;
           // console.log(new Date().toISOString() + ': finished replication');
            cb && cb(err, conflicts);
          });
      });
  }

}



