import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { HttpModule } from '@angular/http';

import { MyApp } from './app.component';



import { IonicStorageModule } from '@ionic/storage';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Device } from '@ionic-native/device';
import { Insomnia } from '@ionic-native/insomnia';
//import { BackgroundGeolocation } from '@ionic-native/background-geolocation';
import { Geolocation } from '@ionic-native/geolocation';
import { LaunchNavigator } from '@ionic-native/launch-navigator';
import { DeviceFeedback } from '@ionic-native/device-feedback';
import { File } from '@ionic-native/file';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { Camera } from '@ionic-native/camera';
import { Network } from '@ionic-native/network';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { Media } from '@ionic-native/media';
import { Push } from '@ionic-native/push';
import { BackgroundMode } from '@ionic-native/background-mode';
import { Diagnostic } from '@ionic-native/diagnostic';



import { SDKBrowserModule } from '../../shared/sdk';
import { LoggerService } from '../../shared/sdk/services/custom/logger.service';



import { LoginServiceProvider } from '../providers/login-service/login-service';
import { ConfigServiceProvider } from '../providers/config-service/config-service';
import { CustomerconnServiceProvider } from '../providers/customerconn-service/customerconn-service';
import { PdaparamsServiceProvider } from '../providers/pdaparams-service/pdaparams-service';
import { SiteconfigServiceProvider } from '../providers/siteconfig-service/siteconfig-service';
import { SharedServiceProvider } from '../providers/shared-service/shared-service';
import { BaseServiceProvider } from '../providers/base-service/base-service';
import { UtilServiceProvider } from '../providers/util-service/util-service';
import { JseaServiceProvider } from '../providers/jsea-service/jsea-service';
import { MessageServiceProvider } from '../providers/message-service/message-service';
import { FixedQueueServiceProvider } from '../providers/fixed-queue-service/fixed-queue-service';
import { DeviceServiceProvider } from '../providers/device-service/device-service';
import { PlatformReadyServiceProvider } from '../providers/platform-ready-service/platform-ready-service';
import { BackgroundGeolocationServiceProvider } from '../providers/background-geolocation-service/background-geolocation-service';
import { GpsAuditServiceProvider } from '../providers/gps-audit-service/gps-audit-service';
import { LogServiceProvider } from '../providers/log-service/log-service';
import { JobServiceProvider } from '../providers/job-service/job-service';
import { EventServiceProvider } from '../providers/event-service/event-service';
import { WindowRefServiceProvider } from '../providers/window-ref-service/window-ref-service';
import { SodServiceProvider } from '../providers/sod-service/sod-service';
import { PushServiceProvider } from '../providers/push-service/push-service';
import { ExitServiceProvider } from '../providers/exit-service/exit-service';
import { GpsServiceProvider } from '../providers/gps-service/gps-service';
import { JobChangedServiceProvider } from '../providers/job-changed-service/job-changed-service';
import { ImageServiceProvider } from '../providers/image-service/image-service';
import { ImageFileServiceProvider } from '../providers/image-file-service/image-file-service';
import { DriverMessageServiceProvider } from '../providers/driver-message-service/driver-message-service';
import { ConnectivityMonitorProvider } from '../providers/connectivity-monitor/connectivity-monitor';
import { SyncServiceProvider } from '../providers/sync-service/sync-service';
import { JobReplicationServiceProvider } from '../providers/job-replication-service/job-replication-service';
import { BarcodeServiceProvider } from '../providers/barcode-service/barcode-service';
import { JobnoteReplicationServiceProvider } from '../providers/jobnote-replication-service/jobnote-replication-service';
import { BarcodeReplicationServiceProvider } from '../providers/barcode-replication-service/barcode-replication-service';
import { JseaAnswersReplicationServiceProvider } from '../providers/jsea-answers-replication-service/jsea-answers-replication-service';


import { NavigationServiceProvider } from '../providers/navigation-service/navigation-service';
import { AttachServiceProvider } from '../providers/attach-service/attach-service';
import { DeviceDiagnosticServiceProvider } from '../providers/device-diagnostic-service/device-diagnostic-service';
import { SqliteServiceProvider } from '../providers/sqlite-service/sqlite-service';


@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    FormsModule,
    IonicModule.forRoot(MyApp, { statusbarPadding: true }),
    SDKBrowserModule.forRoot(),
    IonicStorageModule.forRoot(),
    HttpModule
  ],

  bootstrap: [IonicApp],
  entryComponents: [
    MyApp

  ],
  providers: [
    StatusBar,
    SplashScreen,
    {
      provide: ErrorHandler,
      useClass: IonicErrorHandler
    },
    Diagnostic,
    LoginServiceProvider,
    ConfigServiceProvider,
    CustomerconnServiceProvider,
    PdaparamsServiceProvider,
    SiteconfigServiceProvider,
    SharedServiceProvider,
    BaseServiceProvider,
    UtilServiceProvider,
    JseaServiceProvider,
    MessageServiceProvider,
    FixedQueueServiceProvider,
    DeviceServiceProvider,
    PlatformReadyServiceProvider,
    BackgroundGeolocationServiceProvider,
    Device,
    LogServiceProvider,
    LoggerService,
    GpsAuditServiceProvider,
    //BackgroundGeolocation,
    Insomnia,
    JobServiceProvider,
    EventServiceProvider,
    WindowRefServiceProvider,
    SodServiceProvider,
    PushServiceProvider,
    ExitServiceProvider,
    GpsServiceProvider,
    Geolocation,
    LaunchNavigator,
    JobChangedServiceProvider,
    DeviceFeedback,
    ImageServiceProvider,
    ImageFileServiceProvider,
    File,
    Camera,
    Network,
    DriverMessageServiceProvider,
    BarcodeScanner,
    InAppBrowser,
    FileTransfer,
    FileTransferObject,
    ConnectivityMonitorProvider,
    SyncServiceProvider,
    Media,
    JobReplicationServiceProvider,
    BarcodeServiceProvider,
    Push,
    JobnoteReplicationServiceProvider,
    BarcodeReplicationServiceProvider,
    JseaAnswersReplicationServiceProvider,
    NavigationServiceProvider,
    AttachServiceProvider,
    BackgroundMode,
    DeviceDiagnosticServiceProvider,
    SqliteServiceProvider
  ]
})
export class AppModule { }



