import { Injectable } from '@angular/core';


//import { BackgroundGeolocation, BackgroundGeolocationConfig, BackgroundGeolocationResponse } from '@ionic-native/background-geolocation';
import { Platform } from 'ionic-angular';
import { LoopBackConfig } from '../../../shared/sdk';
import { GpsHistory } from '../../../shared/sdk/models/GpsHistory';
import { GpsHistoryApi } from '../../../shared/sdk/services/custom/GpsHistory';

import { GpsAuditServiceProvider } from '../gps-audit-service/gps-audit-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { LogServiceProvider } from '../log-service/log-service';

import { Device } from '@ionic-native/device';

/*
  Generated class for the BackgroundGeolocationServiceProvider provider.
  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/


@Injectable()
export class BackgroundGeolocationServiceProvider {

  // GPS timestamps are in milliseconds since epoch - iOS also has a fractional part
  // e.g. iOS:		1463707063130.011
  // e.g. Android:	1463707069003
  // Site for millisecond calcs and conversions: http://currentmillis.com/

  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'BackgroundGeolocationServiceProvider' };
  log = this.logger.getInstance(this.logParams);
  lastGPSsecs = 0;		// last timestamp in seconds
  thisGPSsecs = 0;		// this timestamp in seconds
  diffGPSsecs = 0;
  threshold = 1;			// diff between gps must be greater than this to save
  ltimestamp: Date;
  plt;
  osplatform;
  taskKey: any;

  constructor(private pdaParams: PdaparamsServiceProvider, private logger: LogServiceProvider,
    private gpsAudit: GpsAuditServiceProvider, private gpsHistApi: GpsHistoryApi,
    public platform: Platform, private device: Device) {
    this.plt = platform;

    console.log('Hello BackgroundGeolocationServiceProvider Provider');
    //Configure BackgroundGeolocation and set events
    this.setEvents();
  }//End of constructor


  setEvents = () => {
	this.log.debug('setEvents called');

    this.configureBggs();
    this.plt.ready().then((readySource) => {
      this.osplatform = this.device.platform;

		console.log('platform ready, setting BGGS event handlers');
		this.log.debug('platform ready, setting BGGS event handlers');

      if (this.osplatform != null) {

        this.osplatform = this.osplatform.toLowerCase();

        (<any>window).BackgroundGeolocation.on('location', (location) => {
          // handle your locations here 
          // to perform long running operation on iOS
          // you need to create background task

          if (this.osplatform.localeCompare('ios') == 0) {
            (<any>window).BackgroundGeolocation.startTask((taskKey) => {
              // execute long running task
              //this.log.info('Calling saveGpsToDb: location:' + JSON.stringify(location));
              this.saveGpsToDb(this.pdaParams.getDriverId(), location);
              // eg. ajax post location
              // IMPORTANT: task has to be ended by endTask
              (<any>window).BackgroundGeolocation.endTask(taskKey);
            });
          }
          else {
            //this.log.info('Calling saveGpsToDb: location:' + JSON.stringify(location));
            this.log.info("BGGS: Calling saveGpsToDb.");
            this.saveGpsToDb(this.pdaParams.getDriverId(), location);
          }
        });

        (<any>window).BackgroundGeolocation.on('stationary', (stationaryLocation) => {
          // handle stationary locations here
          this.log.info('  BackgroundGeolocation location is stationary.');
        });

        (<any>window).BackgroundGeolocation.on('error', (error) => {
          this.log.debug('  BackgroundGeolocation error:' + error.code + " ,error message:" + error.message);
        });

        (<any>window).BackgroundGeolocation.on('start', () => {
          this.log.info('  BackgroundGeolocation service has been started');
        });

        (<any>window).BackgroundGeolocation.on('stop', () => {
          this.log.info('  BackgroundGeolocation service has been stopped');
        });


        (<any>window).BackgroundGeolocation.on('background', () => {
          this.log.info('  App is in background');
          // you can also reconfigure service (changes will be applied immediately)
          //  (<any>window).BackgroundGeolocation.configure({ debug: true });
        });

        (<any>window).BackgroundGeolocation.on('foreground', () => {
          this.log.info('  App is in foreground');
          // (<any>window).BackgroundGeolocation.configure({ debug: false });
        });
      }
    });//End of platformReady fn  
  }//setEvents() ends


  configureBggs = () => {
    // defaults for background gps   
    this.pdaParams.gps_stopOnTerminate = true;
    this.pdaParams.gps_debug = false;
    this.pdaParams.gps_desiredAccuracy = 10;
    this.pdaParams.gps_stationaryRadius = 20;
    this.pdaParams.gps_activityType = 'AutomotiveNavigation';
    this.pdaParams.gps_distanceFilter = 30;

    this.pdaParams.gps_interval = 60000;            // use this in milliseconds instead   
    this.pdaParams.gps_fastestInterval = 30000;
    this.pdaParams.gps_activitiesInterval = 60000;
    this.pdaParams.gps_stopOnStillActivity = false;


    this.plt.ready().then((readySource) => {

      this.osplatform = this.device.platform;

      if (this.osplatform != null) {

        this.osplatform = this.osplatform.toLowerCase();

        if (this.osplatform.localeCompare('ios') == 0) {
          (<any>window).BackgroundGeolocation.configure({
            locationProvider: (<any>window).BackgroundGeolocation.DISTANCE_FILTER_PROVIDER,
            desiredAccuracy: this.pdaParams.gps_desiredAccuracy,
            distanceFilter: this.pdaParams.gps_distanceFilter,
            stationaryRadius: this.pdaParams.gps_stationaryRadius,
            activityType: this.pdaParams.gps_activityType,			// iOS hint
            debug: this.pdaParams.gps_debug,
            stopOnTerminate: this.pdaParams.gps_stopOnTerminate			// <-- enable this to clear background location settings when the app terminates

          });
        }
        else {
          (<any>window).BackgroundGeolocation.configure({
            locationProvider: (<any>window).BackgroundGeolocation.ACTIVITY_PROVIDER,
            desiredAccuracy: this.pdaParams.gps_desiredAccuracy,
            stationaryRadius: this.pdaParams.gps_stationaryRadius,
            distanceFilter: this.pdaParams.gps_distanceFilter,
            interval: this.pdaParams.gps_interval,// Android - The minimum time interval between location updates in milliseconds 		
            debug: this.pdaParams.gps_debug,					// <-- enable this hear sounds for background-geolocation life-cycle
            stopOnTerminate: this.pdaParams.gps_stopOnTerminate,			// <-- enable this to clear background location settings when the app terminates
            fastestInterval: this.pdaParams.gps_fastestInterval,   //ANDROID ONLY--Fastest rate in milliseconds at which your app can handle location updates
            activitiesInterval: this.pdaParams.gps_activitiesInterval, //ANDROID ONLY--Rate in milliseconds at which activity recognition occurs. Larger values will result in fewer activity detections while improving battery life.
            stopOnStillActivity: this.pdaParams.gps_stopOnStillActivity //ANDROID ONLY--stop() is forced, when the STILL activity is detected (default is true)

          });
        }

      }
    });
  }

  saveGpsToDb = (drvid, location) => {
    //GpsHistory() is a call to the GpsHistory model
    var lgps = new GpsHistory();
    lgps.gps_driver_id = drvid;
    //var platform = $cordovaDevice.getPlatform();

    //this.log.info("BGGS platform:" + this.device_platform + ", location:" + JSON.stringify(location));
    // LT - 06/07/2016 - with 2.52 release iOS devices do not appear to have a location.timestamp property
    // but do have the location.time property - check for both
    if (this.osplatform.localeCompare('ios') == 0) {
      if (location.timestamp) {
        lgps.gps_timestamp = location.timestamp;
      }
      else if (location.time) {
        lgps.gps_timestamp = location.time;
      }
      else {
        // lgps.gps_timestamp = Date.now();
        lgps.gps_timestamp = new Date();
        this.log.error("BGGS: no time provided so use now:" + lgps.gps_timestamp);
      }
      lgps.gps_heading = 0; // IOS Has a heading if u want to use it later
    }
    else {
      lgps.gps_timestamp = location.time;
      lgps.gps_heading = 0; // Android has a bearing if u want to use it later
    }

    //this.log.info("lgps.gps_timestamp:: " + lgps.gps_timestamp);
    this.ltimestamp = new Date(lgps.gps_timestamp);
    //this.log.info("this.ltimestamp:: " + this.ltimestamp);

    // check last GPS and only update if diff is greater than x seconds
    this.thisGPSsecs = this.ltimestamp.getTime() / 1000;				// current GPS time in seconds
    this.diffGPSsecs = this.thisGPSsecs - this.lastGPSsecs;
    this.lastGPSsecs = this.ltimestamp.getTime() / 1000;

    // lgps.gps_timestamp is UTC at this point
    //var ldate = new Date(lgps.gps_timestamp);
    //var oset = ldate.getTimezoneOffset();

    // lgps.gps_timestamp converted to local time - no timezone now
    //var temp: any = this.ltimestamp.getTime() + (oset * -1) * 60 * 1000;
    //this.log.info("temp:::" + temp);
    //this.log.info("temp with offset::" + this.ltimestamp.getTime() + (oset * -1) * 60 * 1000);

    if (location.speed !== null && location.speed !== undefined)
      lgps.gps_speed = location.speed;
    else
      lgps.gps_speed = 0;
    lgps.gps_timestamp = this.ltimestamp;
    lgps.gps_latitude = location.latitude;
    lgps.gps_longitude = location.longitude;
    lgps.gps_quality = location.accuracy;
    lgps.gps_time = 0;//location.time;

    // TODO - need to NOT rely on log - need our own access to this function
    var loggedOn = this.log.pdaParams.isDrvLoggedOn();
    //this.log.info("BGGS driver:" + lgps.gps_driver_id + ", loggedOn:" + loggedOn + ", lgps:" + JSON.stringify(lgps));
    this.gpsAudit.saveGps(lgps);

    //Riyaz:Now change the lgps.timestamp to oracle date, before saving it to the oracle DB.
    //lgps.gps_timestamp = new Date(lgps.gps_timestamp.valueOf());

    // TODO - do we need any more criteria to create history record?  if connected?
    if (lgps.gps_driver_id > 0 && this.diffGPSsecs > this.threshold && loggedOn) {
      // this.log.info("About to save BGGS GPS for driver:" + lgps.gps_driver_id + ", lgps:" + JSON.stringify(lgps));
      this.gpsHistApi.create(lgps).subscribe(
        obj => {
          this.log.info("gpsHistApi.create success for BGGS: obj:" + JSON.stringify(obj));
        },
        err => {
          this.log.error("gpsHistApi.create failed for BGGS: err:" + JSON.stringify(err));
        }
      );
    }
  }//saveGpsToDb() ends here


  /*=====================================================*/
  /*             START METHOD                            */
  /*=====================================================*/
  start = () => {
    this.configureBggs();

    // start recording location
    this.log.info("BGGS about to start...");
    (<any>window).BackgroundGeolocation.start();
  }

  /*=====================================================*/
  /*             STOP METHOD                            */
  /*=====================================================*/
  stop = () => {
    // If we wish to turn OFF background-tracking, we can call the #stop method.
    this.log.info('BGGS STOP Method called:');
    (<any>window).BackgroundGeolocation.stop();
  }

  bggs_print_configuration = () => {
    this.log.info("=====================================================================");

    this.log.info("this.pdaParams.gps_locationProvider::" + this.pdaParams.gps_locationProvider);
    this.log.info("this.pdaParams.gps_activityType::" + this.pdaParams.gps_activityType);
    this.log.info("this.pdaParams.gps_debug::" + this.pdaParams.gps_debug);
    //this.log.info("this.pdaParams.gps_desiredAccuracy::" + this.pdaParams.gps_desiredAccuracy);
    this.log.info("this.pdaParams.gps_distanceFilter::" + this.pdaParams.gps_distanceFilter);
    this.log.info("this.pdaParams.gps_interval::" + this.pdaParams.gps_interval);
    this.log.info("this.pdaParams.gps_stationaryRadius::" + this.pdaParams.gps_stationaryRadius);
    this.log.info("this.pdaParams.gps_stopOnTerminate::" + this.pdaParams.gps_stopOnTerminate);
    this.log.info("=====================================================================");
  }

}



