import { Injectable } from '@angular/core';

import { Geolocation } from '@ionic-native/geolocation';
import { Device } from '@ionic-native/device';

import { GpsHistory } from '../../../shared/sdk/models/GpsHistory';

import { GpsHistoryApi } from '../../../shared/sdk/services/custom/GpsHistory';
import { GpsAuditServiceProvider } from '../gps-audit-service/gps-audit-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { SharedServiceProvider } from '../shared-service/shared-service';
import { LogServiceProvider } from '../log-service/log-service';


/*
  Generated class for the GpsServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.

  Refer: https://ionicframework.com/docs/native/geolocation/  
*/
@Injectable()
export class GpsServiceProvider {

  // TODO - set high accuracy with a toggle parameter
  // TODO - set maximumAge to 0?  Apparently Samsung Galaxy phone have issues caching the gps location
  // (see http://stackoverflow.com/questions/16262878/phonegap-geolocation-code-3-timeout-expired-keeps-popping-up-on-some-android)
  // Apparently a reboot can fix Android gps timeouts

  // example here: http://www.gajotres.net/using-cordova-geoloacation-api-with-google-maps-in-ionic-framework/

  //HIGH ACCURACY may be problematic

  //var posOptions = {timeout: 1000, frequency : 1000, enableHighAccuracy: true };
  posOptions = { timeout: 10000, enableHighAccuracy: false };
  gpsIsWorking = false;
  numsaves = 0;
  lastGPSsecs = 0;		// last timestamp in seconds
  thisGPSsecs = 0;		// this timestamp in seconds
  diffGPSsecs = 0;
  getGps = true;
  showGpsAlerts = false;
  sendGps = true;
  gpsData = {};
  ltimestamp: Date;
  threshold = 1;			// diff between gps must be greater than this to save
  platform: any;
  logParams = {
    site: this.pdaParams.getSiteId(),
    driver: this.pdaParams.getDriverId(), fn: 'GpsServiceProvider'
  };
  log = this.logger.getInstance(this.logParams);

  constructor(private pdaParams: PdaparamsServiceProvider,
    private logger: LogServiceProvider,
    private gpsAudit: GpsAuditServiceProvider,
    private gpsHistApi: GpsHistoryApi,
    private geolocation: Geolocation, private device: Device,
    private sharedService: SharedServiceProvider) {
    this.platform = this.device.platform;
    //console.log('Hello GpsServiceProvider Provider');

    this.sharedService.RESUME.subscribe(val => {
      // foreground
      this.log.info("RESUME event fired: ForeGround GPS about to START.");
      this.getGps = true;
      this.getPos();
    });

    this.sharedService.PAUSE.subscribe(val => {
      // background
      this.log.info("PAUSE event fired: ForeGround GPS about to STOP.");
      this.getGps = false;
    });


  }//Constructor ends here

  getPos = () => {
	console.log('getPos: FGGPS getGps:'+this.getGps);
	this.log.debug('getPos: FGGPS getGps:'+this.getGps);
    if (this.getGps) {
      if (this.log.context.driver == 0) {
        this.log.context.driver = this.pdaParams.getDriverId();
      }

      if (!this.pdaParams.alwaysGetGPS) {
        // If driver logged off or app in background don't bother with any of this
        if (this.pdaParams.isDrvLoggedOff()) {
          this.log.info('Driver logged off, no foreground GPS attempted');
          return;
        }
      }
      //Riyaz: Using ionic native's geolocation plugin here
      this.geolocation.getCurrentPosition(this.posOptions).then((position) => {

        if (this.showGpsAlerts) {
          this.log.debug(' GPSService: Position returned ');
        }
        //this.gpsData = angular.copy(position);
        this.gpsData = Object.assign({}, position);
        this.log.debug("position:" + JSON.stringify(this.gpsData));
        this.saveGpsToDb(this.pdaParams.getDriverId(), position);
        this.gpsIsWorking = true;
      }).catch((err) => {
        if (this.showGpsAlerts) {
          this.log.debug('GPsService Error code: ' + err.code + '\n' + 'message: ' + err.message + '\n');
        }
        // error
        this.gpsIsWorking = false;
        var error = this.getGPSErrors(err);

        this.log.error("getCurrentPosition failed with error :" + error);
      });
    }

  } //getPos() ends here

  saveGpsToDb = (drvid, position) => {
    //GpsHistory() is a call to the GpsHistory model
    var lgps = new GpsHistory();
    lgps.gps_driver_id = drvid;
    //lgps.gps_timestamp = new Date(position.timestamp);
    lgps.gps_timestamp = position.timestamp;

    if (position.speed !== null && position.speed !== undefined)
      lgps.gps_speed = position.speed;
    else
      lgps.gps_speed = 0;

    lgps.gps_latitude = position.coords.latitude.toFixed(6);
    lgps.gps_longitude = position.coords.longitude.toFixed(6);
    lgps.gps_quality = position.coords.accuracy;
    lgps.gps_heading = 0; // position.coords.heading;   
    lgps.gps_time = 0; //new Date(location.timestamp).getTime();
    //this.log.debug("getCurrentPosition OK: sendGps:" + this.sendGps + ", lgps:" + JSON.stringify(lgps));

    this.gpsAudit.saveGps(lgps);

    //lgps.gps_timestamp = position.timestamp;

    // TODO - do we need any more criteria to create history record?  if connected?
    if (this.sendGps && lgps.gps_driver_id > 0) {
      this.log.info("About to save Foreground GPS");

      this.gpsHistApi.create(lgps).subscribe(
        obj => {
          this.log.info("gpsHistApi.create success for Foreground GPS: obj:" + JSON.stringify(lgps));
        },
        err => {
          this.log.error("gpsHistApi.create failed for Foreground GPS: err:" + JSON.stringify(err));
        }
      );
    }
  }//saveGpsToDB(drvid, position) ends here

  onSuccess(position) {
    this.gpsIsWorking = true;
  }
  onError(error) {
    if (this.showGpsAlerts) {
      this.log.error('GPSservice OnOFFPos  Error code: ' + error.code + '\n' + 'message: ' + error.message + '\n');
    }
    this.gpsIsWorking = false;
  }

  //Every 60 Seconds
  intervalId = setInterval(this.getPos, 60000);

  getGpsIsWorking() {
    //getOneoFFPos();
    return this.gpsIsWorking;
  }

  getShowGpsAlerts() {
    //getOneoFFPos();
    return this.showGpsAlerts;
  }

  getSendGps() {
    //getOneoFFPos();
    return this.sendGps;
  }

  setShowGpsAlerts(torf) {
    this.showGpsAlerts = torf;
    localStorage.setItem('showGpsAlerts', JSON.stringify(this.showGpsAlerts));
  }

  setSendGps(torf) {
    this.sendGps = torf;
    localStorage.setItem('sendGps', JSON.stringify(this.sendGps));
    this.log.debug("setSendGps now:" + this.sendGps);
  }

  toggleShowGpsAlerts() {
    this.showGpsAlerts = !this.showGpsAlerts;
    localStorage.setItem('showGpsAlerts', JSON.stringify(this.showGpsAlerts));
  }

  // LT - code added for background geolocation processing

  getCurrentPos(cb) {
    this.geolocation.getCurrentPosition(this.posOptions).then((position) => {
      cb(null, position);
    }).catch((err) => {
      cb(err, null);
    });
  }//getCurrentPos(cb) ends here

  getGPSErrors(err) {
    var error;
    switch (err.code) {
      case err.PERMISSION_DENIED:
        error = "Failed to retrieve the location of the device because the application does not have permission to use the Location Service."
        break;
      case err.POSITION_UNAVAILABLE:
        error = "The location of the device could not be determined."
        break;
      case err.TIMEOUT:
        error = "Unable to retrieve the location information within the specified maximum timeout interval."
        break;
      case err.UNKNOWN_ERROR:
        error = "Failed to retrieve the location of the device due to an unknown error."
        break;
    }
    return error;
  }

}



