
import { Injectable } from '@angular/core';
import { Diagnostic } from '@ionic-native/diagnostic';

import { Platform } from 'ionic-angular';

import { DeviceDiagnostics } from '../../../shared/sdk/models/DeviceDiagnostics';
import { DeviceDiagnosticsApi } from '../../../shared/sdk/services/custom/DeviceDiagnostics';

import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { LogServiceProvider } from '../log-service/log-service';
import { SiteconfigServiceProvider } from '../siteconfig-service/siteconfig-service';
import { SharedServiceProvider } from '../shared-service/shared-service';

import { Device } from '@ionic-native/device';

/*
  Generated class for the DeviceDiagnosticServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class DeviceDiagnosticServiceProvider {
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'DeviceDiagnosticServiceProvider' };
  log = this.logger.getInstance(this.logParams);
  driverId: Number = 0;
  lastPingTimestamp: Date = new Date();
  isLocationAvailable: boolean = false;
  isLocationEnabled: boolean = false;
  isGpsLocationAvailable: boolean = false;
  isGpsLocationEnabled: boolean = false;
  isNetworkLocationAvailable: boolean = false;
  isNetworkLocationEnabled: boolean = false;
  getLocationMode: string = "Location off";
  isLocationAuthorized: boolean = false;
  isWifiAvailable: boolean = false;
  isWifiEnabled: boolean = false;
  isBluetoothAvailable: boolean = false;
  intrval;
  pda_disable_device_diagnostics;


  constructor(private pdaParams: PdaparamsServiceProvider, private logger: LogServiceProvider, private diagnostic: Diagnostic,
    private siteConfig: SiteconfigServiceProvider, private deviceDiagnosticsApi: DeviceDiagnosticsApi, public platform: Platform,
    private device: Device, private sharedService: SharedServiceProvider) {
    console.log('Hello DeviceDiagnosticServiceProvider Provider');
	this.log.debug("constructor");

    this.pda_disable_device_diagnostics = (this.pdaParams.pda_disable_device_diagnostics || (this.siteConfig.getSiteConfigValue('PDA_DISABLE_DEVICE_DIAGNOSTICS') == 'Y'));

    if (!this.pda_disable_device_diagnostics)
      this.runDiagnostics();

  }

  runDiagnostics = () => {
	this.log.debug("runDiagnostics");

    this.platform.ready().then((readySource) => {
		this.log.debug("runDiagnostics platform is ready, platforms:"+this.platform.platforms());

      if (this.platform.is('cordova')) {
        let errorCallback = (diagFn) => this.log.error(diagFn + " : failed to diagnose from the device.");

        this.diagnostic.isLocationAvailable().then((val) => { this.isLocationAvailable = val; }, () => { });
        this.diagnostic.isLocationEnabled().then((val) => { this.isLocationEnabled = val; }, () => { });
        this.diagnostic.isGpsLocationAvailable().then((val) => { this.isGpsLocationAvailable = val; }, () => { });
        this.diagnostic.isGpsLocationEnabled().then((val) => { this.isGpsLocationEnabled = val; }, () => { });
        this.diagnostic.isNetworkLocationAvailable().then((val) => { this.isNetworkLocationAvailable = val; }, () => { });
        this.diagnostic.isNetworkLocationEnabled().then((val) => { this.isNetworkLocationEnabled = val; }, () => { });
        this.diagnostic.isLocationAuthorized().then((val) => { this.isLocationAuthorized = val; }, () => { });
        this.diagnostic.isWifiAvailable().then((val) => { this.isWifiAvailable = val; }, () => { });
        this.diagnostic.isWifiEnabled().then((val) => { this.isWifiEnabled = val; }, () => { });
        this.diagnostic.isBluetoothAvailable().then((val) => { this.isBluetoothAvailable = val; }, () => { });


        if (!this.platform.is('ios')) {
          this.diagnostic.getLocationMode()
            .then((locationMode) => {
              switch (locationMode) {
                case this.diagnostic.locationMode.HIGH_ACCURACY:
                  this.getLocationMode = "High accuracy";
                  // console.log("High accuracy");
                  break;
                case this.diagnostic.locationMode.BATTERY_SAVING:
                  this.getLocationMode = "Battery saving";
                  //console.log("Battery saving");
                  break;
                case this.diagnostic.locationMode.DEVICE_ONLY:
                  this.getLocationMode = "Device only";
                  //console.log("Device only");
                  break;
                case this.diagnostic.locationMode.LOCATION_OFF:
                  this.getLocationMode = "Location off";
                  //console.log("Location off");
                  break;
              }
            }).catch(e => console.error(e));
        } else
          this.getLocationMode = "Not Available";

      }
    });

  }

  diagnosticPoller = () => {
	console.log("diagnosticPoller");
	this.log.debug("diagnosticPoller");

    this.runDiagnostics();

    var dd = new DeviceDiagnostics();
    dd.driverId = this.pdaParams.getDriverId();

    var lastPingDate = new Date();
    this.lastPingTimestamp = new Date(lastPingDate.getTime());
    dd.lastPingTimestamp = this.lastPingTimestamp;

    dd.getLocationMode = this.getLocationMode;
    dd.isGpsLocationAvailable = this.isGpsLocationAvailable;
    dd.isGpsLocationEnabled = this.isGpsLocationEnabled;
    dd.isLocationAuthorized = this.isLocationAuthorized;
    dd.isLocationAvailable = this.isLocationAvailable;
    dd.isLocationEnabled = this.isLocationEnabled;
    dd.isNetworkLocationAvailable = this.isNetworkLocationAvailable;
    dd.isNetworkLocationEnabled = this.isNetworkLocationEnabled;
    dd.isWifiAvailable = this.isWifiAvailable;
    dd.isWifiEnabled = this.isWifiEnabled;

    this.localStorageUsage();

    this.deviceDiagnosticsApi.upsert(dd).subscribe(
      obj => {
        this.log.info("deviceDiagnosticsApi.create success : " + JSON.stringify(obj));
      },
      err => {
        this.log.error("deviceDiagnosticsApi.create failed : " + JSON.stringify(err));
      }
    );
  }

  localStorageUsage() {
    var x, xLen, usage = 0;
    for (x in localStorage) {
      if (!localStorage.hasOwnProperty(x)) { continue; }

      xLen = ((localStorage[x].length * 2 + x.length * 2) / 1024);
      usage += xLen
    };

    if (usage > 1024) {
      this.log.info("LocalStorage used by the app is ::" + (usage / 1024).toFixed(2) + " MB");
    }
    else {
      this.log.info("LocalStorage used by the app is ::" + usage.toFixed(2) + " KB");
    };

  }

  stopDiagnostics() {
    this.log.info("Stopping device diagnostics...");
    //if (!this.pda_disable_device_diagnostics) {
    this.sharedService.isDiagnosticsRunning = false;
    clearInterval(this.intrval);
    //}
  }

  startWatching() {
    //Call the Poller every minute
    this.pda_disable_device_diagnostics = (this.pdaParams.pda_disable_device_diagnostics || (this.siteConfig.getSiteConfigValue('PDA_DISABLE_DEVICE_DIAGNOSTICS') == 'Y'));
	var running = this.sharedService.isDiagnosticsRunning;

	console.log("startWatching: DeviceDiagnosticServiceProvider: disable_device_diagnostics:"+this.pda_disable_device_diagnostics + ', running:'+running);
	this.log.debug("startWatching: disable_device_diagnostics:"+this.pda_disable_device_diagnostics + ', running:'+running);

    if (!this.pda_disable_device_diagnostics) {
			// only start polling service if not already running
			if(!running) {
				this.sharedService.isDiagnosticsRunning = true;
				this.intrval = setInterval(this.diagnosticPoller, 60000);
			}
    }
  }

  

}



