import { Injectable } from '@angular/core';
import { LaunchNavigator } from '@ionic-native/launch-navigator';

import { LogServiceProvider } from '../log-service/log-service';
import { PdaparamsServiceProvider } from '../pdaparams-service/pdaparams-service';
import { SharedServiceProvider } from '../shared-service/shared-service';
import { SiteconfigServiceProvider } from '../siteconfig-service/siteconfig-service';
import { PlatformReadyServiceProvider } from '../platform-ready-service/platform-ready-service';
/*
  Generated class for the NavigationServiceProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/


@Injectable()
export class NavigationServiceProvider {
  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'NavigationServiceProvider' };
  log = this.logger.getInstance(this.logParams);
  isReady = false;
  moduleInstalled = false;
  SITE_CONFIG_LOADED: boolean = false;
  navigationServAllowed = false;
  //myNotReadyErr = "Sorry Cordova Is Not ready, until this occurs cannot use Navigator Plugin";
  myNotModuleInstalled = "Sorry this module is not installed, please contact your office";
  myJobNumber = 0;
  myJobDate = 0;
  myaddress = '';

  constructor(
    private logger: LogServiceProvider,
    private pdaParams: PdaparamsServiceProvider,
    private platformReadyService: PlatformReadyServiceProvider,
    private sharedService: SharedServiceProvider,
    private siteConfig: SiteconfigServiceProvider,
    public navigatorPlugin: LaunchNavigator) {

    var lstr = 'Constructing NavigationServiceProvider Provider';
    // console.log(lstr);
    this.log.debug(lstr);

    //this.sharedService.SITE_CONFIG_LOADED.subscribe(SITE_CONFIG_LOADED => {
    this.prepNavigation();
  }

  prepNavigation = () => {
    if (localStorage.getItem('SITE_CONFIG_LOADED')) {
      //if (localStorage.getItem('SITE_CONFIG_LOADED')) {
      //this.log.debug('NavigationServiceProvider SITE_CONFIG_LOADED Event triggered');
      if (this.siteConfig.getSiteConfigValue('PDA_REQ_NAVIGATION') == 'Y') {
        this.moduleInstalled = true;

        this.navigationServAllowed = true;
        //this.log.debug("navigationServAllowed is NOW TRUE");
        this.isReady = true;
        // }
      }
      else {
        // this.log.debug("navigationServAllowed:" + this.myNotModuleInstalled);
        //alert(this.myNotModuleInstalled);
      }
    }
  }

  isRequired = function () {
    return this.isReady;//this.myNotModuleInstalled;
  }

  isModuleInstalled = function () {
    return this.moduleInstalled;
  }


  registerJobDetails = function (jobnum, jobdate, address) {
    //this.log.debug("Registering jobDetails num " + jobnum + " Date " + jobdate + ' Address = ' + address);
    this.JobNumber = jobnum;
    this.JobDate = jobdate;
    this.myaddress = address;
  };

  onSuccessCallback = function () {
    //navigator.notification.alert("Successfully launched navigator");
    this.log.debug("Successfully launched navigator for ");
  };

  onErrorCallback = function (errMsg) {
    this.log.debug("Failed to  launch navigator error = " + errMsg);
  };
  clearUserChoice = function () {

    this.log.debug("User wants Wants to clear preferred app");

    if (!this.navigationServAllowed) {
      //alert(this.myNotModuleInstalled);
      this.log.debug(this.myNotModuleInstalled);
      return;
    }

    /* this.navigatorPlugin.appSelection.userChoice.get(function (app) {
      this.log.debug("User preferred app currently is: " + this.navigatorPlugin.getAppDisplayName(app));
    });
 */

    this.navigatorPlugin.appSelection.userChoice.clear(function () {
      this.log.debug("Wants to clear app ,so now preferred app is cleared");
    });

    alert("Your navigator choice has been cleared, you will be prompted the next time you decide to navigate");
  };


  navigate = (deststr) => {
    this.log.debug('Navigate: Inside navigate() with deststr:' + deststr);

    this.prepNavigation();
    this.log.debug('Navigate: Returned from prepNavigation() with:::');
    this.log.debug('Navigate: this.moduleInstalled::' + this.moduleInstalled);
    this.log.debug('Navigate: this.navigationServAllowed::' + this.navigationServAllowed);
    this.log.debug('Navigate: this.isReady::' + this.isReady);


    if (!deststr) {
      //navigator.notification. alert("A destination must be specified");
      // alert("A destination must be specified");
      this.log.debug("A destination must be specified");
      return;
    }

    if (this.isReady) {
      this.log.debug('Navigate: inside this.isReady');
      this.log.debug('Navigate: Value of this.navigatorPlugin::' + this.navigatorPlugin);
      this.log.debug('Navigate: ::this.navigatorPlugin.APP::' + this.navigatorPlugin.APP);
      

      this.navigatorPlugin.navigate(deststr, {
        start: "",
        enableDebug: true
      }).then(
        success => alert('Launched navigator'),
        error => alert('Error launching navigator: ' + error)
      );

      /* .then(success =>
              this.log.debug('navigatorPlugin.navigate success::'),
              error =>
                this.log.error('navigatorPlugin.navigate error::' + error)
            ); */
    }
    else {
      //alert("cordovaReady.isDeviceReady = false, cant use Navigator Plugin");
      this.log.debug("cordovaReady.isDeviceReady = false, cant use Navigator Plugin");
    }
  };

}


